.a-radio{
    position: absolute;
    opacity: 0;
    width: 1px;
    height: 1px;
    & + label{
        display: flex;
        align-items: center;
        margin: 0 auto;
        cursor: pointer;
        
        &:before{
            content: '';
            height: 15px;
            width: 15px;
            display: block;
            margin-right: 13px;
            background-color: transparent;
            border: solid 2px $core-color;
            border-radius: 50%;
            flex-shrink: 0;
        }
    }
    &:checked + label{
        &:before{
            background-color: $core-color;
        }
    }
}