.webform-grid{
    margin-bottom: 0 !important;
    .form__label{
        padding-right: 0 !important;
        width: 100% !important;
    }
    .a-radio + label:before{
        margin-right: 0;
    }
    .a-radio + label{
        justify-content: center;
    }
    .webform-likert-label{
        display: none !important;
    }
    .tablesaw-cell-label{
        @extend .a-label;
        text-align: left;
        width: 80% !important;
    }
    @at-root .m-generic-form--sbs &{
        .form__item{
            width: 100%;
        }
        .form__label{
            justify-content: flex-start !important; 
        }
        .a-form-infos{
            padding-left: 0 !important;
        }
        .a-label{
            text-align: left !important;
        }
    }
}