.hp-kiosque{
    padding: calc(100vw / 19) calc(50vw / 19);
    background: $second-color;

    @include RWD(mobile){
        margin-left: -20px;
        margin-right: -20px;
        padding: 50px 20px;
    }

    &__list{}
    &__card{
        display: flex;
        @include RWD(mobile){
            display: block;
        }
    }
    &__card + &__card{
        margin-top: 50px;

        @include RWD(mobile){
            margin-top: 100px;
        }
    }
    &__img{
        width: 200px;
        margin-right: 30px;
        background-position: top center;
        background-size: contain;
        background-repeat: no-repeat;
        flex-shrink: 0;
        transform: translateY(3px);
        
        @include RWD(mobile){
            width: 150px;
            margin: 0 auto;
            height: 195px;
            background-size: cover;
            margin-bottom: 25px;
        }
    }
    &__text{
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        & > * {
            max-width: 100%;
        }

        @include RWD(mobile){
            align-items: center;
        }
    }
    &__title{
        font-family: $fancy-font;
        font-size: 3.4rem;
        font-weight: 700;
        line-height: 1.2em;
        color: $core-color;

        @include RWD(mobile){
            font-size: 2.4rem;
            text-align: center;
        }
    }
    &__ref{
        font-family: $fancy-font;
        font-size: 1.7rem;
        font-weight: 700;
        line-height: 1.2em;
        color: $core-color;
        text-transform: uppercase;

        @include RWD(mobile){
            font-size: 1.5rem;
            text-align: center;
        }
    }
    &__lead{
        font-family: $main-font;
        font-size: 1.7rem;
        font-weight: 400;
        line-height: 1.2em;
        color: $core-color;

        @include RWD(mobile){
            font-size: 1.5rem;
            text-align: center;
        }
    }
    .filler{
        flex: 1;
        min-height: 35px;

        @include RWD(mobile){
            min-height: 25px;
        }
    }
    &__see{
        
    }
    &__download{
        margin-top: 5px;
    }
    &__more{
        margin-top: 40px;
        display: flex;
        justify-content: center;
    }
}