.#{$sfull_name}{
    &-thumbnail{
        z-index: 2;
        transform: translateY(-140px);
        margin-bottom: -100px;

        @include RWD(tablet){
            transform: translateY(-50px);
            margin-bottom: -25px;
        }
        
        @extend .smag-container;

        @include RWD(mobile){
            margin-top: 20px;
            margin-bottom: -50px;
        }
        
        /* Stage outer
        ============================= */
        
        .owl-stage{
            @include RWD(desktop){
                transform: translateX(-30%);                
            }
        }
        .owl-stage-outer{ // Laisser apparaitre le box shadow sous les li
            padding-bottom: 30px;
            margin-bottom: -30px;
            padding-top: 30px;
            margin-top: -30px;
        }

        /* ITEM
        ============================= */
        
        &__slider-item{
            cursor: pointer;
            box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.2);
            transition: .3s;

            &:hover{
                @include RWD(desktop){
                    transform: translateY(-20px);                    
                }
            }
        }
        &__slide{

            &--video{
                &:before{display: block;}
                .owl-video-tn{
                    position: absolute;
                    top: 0; left: 0;
                    height: 100%;
                    width: 100%;
                }
                .owl-video-play-icon{
                    display: none;
                }
                .owl-video-wrapper{
                    position: absolute;
                    top: 0; left: 0;
                    height: 100%;
                    width: 100%;
                    
                    &:before{
                        display: none;
                    }
                }
            }
            &-image{
                position: relative;
                height: auto;
                padding-bottom: 67.53%;
            }
            &-background{
                position: absolute;
                top: 0; left: 0;
                height: 100%;
                width: 100%;
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                
                &:before{
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 2;
                    height: 100%;
                    width: 100%;
                    background-color: rgba(#000000, 0.5);
                    background-image: url( svg-bg-uri($svg-morecircled, #FFFFFF) );
                    background-position: center;
                    background-size: 40px 40px;
                    background-repeat: no-repeat;
                    opacity: 0;
                    transition: all, 0.5s;
                }
            }
            &-text{
                padding: 25px 25px 50px 25px;
                background-color: $core-color-dark;
                transition: .3s;
                @include RWD(tablet){
                    display: none;
                }
            }
            &-title{
                font-family: $fancy-font;
                font-size: 1.9rem;
                font-weight: 700;
                text-align: center;
                color: #FFFFFF;
                text-transform: uppercase;
                min-height: 2.4em;
                transition: .3s;
                line-height: 1.2em;
            }
            &-description{
                font-family: $main-font;
                font-weight: 500;
                text-align: center;
                color: #b6bcc3;
                min-height: 2.4em;
                transition: .3s;
                line-height: 1.2em;
                margin-top: 5px;
            }
        }
        .owl-item.center &__slide-background:before{
            opacity: 0;
        }
        .owl-item.center &__slide-title{
            color: #505050;
        }
        .owl-item.center &__slide-description{
            color: #7e7e7e;
        }
        .owl-item.center &__slide-text{
            background-color: #FFFFFF;
        }

        
        /* ARROWS
        ============================= */
        &__nav{
            position: absolute;
            top: 0;
            left: 80px;
            z-index: 2;
            display: flex;
            align-items: center;
            transform: translateY(-45px) translateY(30px);
            @include RWD(tablet){
                left: 40px;
            }
            @include RWD(mobile){
                left: 20px;
                right: 20px;
                justify-content: space-between;
            }

            &-title{
                order: 2;
                margin-left: 15px;
                font-family: $fancy-font;
                font-size: 1.7rem;
                font-weight: 700;
                color: #FFFFFF;
                text-transform: uppercase;

                @include RWD(mobile){
                    margin-right: 10px;
                }
            }
        }
        &__arrow{
            position: static;
            transition: .3s;
            @include RWD(mobile){
                // display: none;
            }

            .flexbox{
                background-color: #FFFFFF;
                border-radius: 50%;
                border: solid 3px #FFFFFF;
                transition: .3s;
                height: 60px;
                width: 60px;
            }

            &-icon{
                width: 27px;
                height: 22px;
                background-image: url( svg-bg-uri($svg-arrow1, $core-color) );
                transition: .3s;
            }
            &-pagination{
                display: none;
            }
            &--prev{
                @include RWD(mobile){
                    order: 1;
                }
            }
            &--next{
                @include RWD(mobile){
                    order: 3;
                }
            }
            &--total{}
            &.disabled{
                pointer-events: none;
                .flexbox{
                    background-color: transparent;
                }
            }
            &.disabled &-icon{
                background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
            }
        }
        &__arrow + &__arrow{
            margin-left: 5px;
        }   
        // Hover des flches
        &__arrow:hover .flexbox{
            background-color: $core-color;
        }  
        &__arrow:hover &__arrow-icon{
            background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
        }  

        /* Pager
        ============================= */
        
        &__pager{
            &--current{
            }
            &--prev{
            }
            &--next{
            }
        }

        
        /* DOTS
        ============================= */
        
        &__dot{
            

            &.active{
                
            }
        }

        /* Playpause
        ============================= */
        
        &__playpause{
            display: none;
            &.play{
                
            }
            &.pause{
                
            }
        }

        /* Slider Text
        ============================= */

        &__text {
            display: none !important;
        }
    }
}