.a-input-text{
    height: em($form-el-height);
    width: 100%;
    padding: 0 15px;
    background-color: #fff;
    font-family: $main-font;
    font-size: #{$browser-context / 10}rem;
    line-height: 1.2em;
    color: $text-color;
    border: 1px solid #d0d0d0;
    border-radius: 0; 
    -webkit-appearance: none;

    @include RWD(mobile){
        font-size: 1.6rem;
    }

    &:focus{
        border-color: $core-color;
        outline: none;
    }
    &::-webkit-inner-spin-button, 
    &::-webkit-outer-spin-button {  
        align-self: center;
    }
    &.form-autocomplete{
        background-position: right 20px center !important;
    }
} 