.a-audio-player{
    width: 655px;
    display: flex;
    margin: 0 auto;
    background-color: #FFFFFF;
    max-width: 100%;
    @include RWD(mobile){
        display: block;
        padding: 20px;
    }


    audio{
        display: none;
    }

    // Visuel
    .audio-visu{
        text-align: center;
        @include RWD(mobile){
            margin-bottom: 20px;
        }
        img{
            height: 140px;
            width: 140px;
        }
    }

    // Partie droite infos
    .audio-infos{
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        padding: 20px;

        @include RWD(mobile){
            padding: 0px;
        }
    }

    .audio-title{
        margin-bottom: 10px;
        font-size: 24px;
        font-size: 2.4rem;
        font-weight: 600;
        color: #1c1c1c;
    }
    .audio-artist{
        margin-bottom: 5px;
        font-size: 16px;
        font-size: 1.6rem;
        font-weight: 400;
        color: #1C1C1C;

        @include RWD(mobile){
            margin-bottom: 20px;
        }
    }

    // Lecteur audio
    .audio-player{
        width: 360px;
        display: flex;
        max-width: 100%;

        // Bouton playpause
        .player-control{
            height: 30px;
            width: 25px;
            margin-right: 20px;
            cursor: pointer;
            transition: all, 0.5s;
            .flexbox{
                display: flex;
                justify-content: center;
                align-items: center;
            }

            .btn-play{
                height: 0;
                width: 0;
                display: block;
                border-style: solid;
                border-width: 15px 0 15px 25px;
                border-color: transparent transparent transparent #42424b;
            }
            .btn-pause{
                height: 20px;
                width: 15px;
                display: none;
                justify-content: space-between;
                margin: 0 auto;
                &:before{
                    content: "";
                    height: 100%;
                    width: 4px;
                    display: block;
                    background-color: #42424b;
                }
                &:after{
                    content: "";
                    height: 100%;
                    width: 4px;
                    display: block;
                    background-color: #42424b;
                }
            }
            .hidden{
                display: none;
            }

            &.playing{
                .btn-play{
                    display: none;
                }
                .btn-pause{
                    display: flex;
                }
            }
            &:hover, &:focus{
                opacity: 0.5;
            }
        }

        // Vue bar + temps
        .player-view{
            flex: 1;
            .player-timing{
                display: flex;  
                justify-content: space-between;
                margin-bottom: 5px;

                .player-time-current{
                    font-size: 11px;
                    font-size: 1.1rem;
                    color: #1c1c1c;
                }
                .player-time-total{
                    font-size: 11px;
                    font-size: 1.1rem;
                    color: $core-color;
                }
            }
            .player-progress-bar{
                position: relative;
                height: 5px;
                width: 100%;
                overflow: hidden;
                background-color: #d6dde2;
                border-radius: 5px;

                input[type="range"]{
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 10;
                    width: 100%;
                    margin: 0;
                    cursor: pointer;
                    transform: translateY(-50%);
                    opacity: 0;
                }
                .player-running-bar{
                    position: absolute;
                    top: 0;
                    left: 0; 
                    height: 100%;
                    width: 0%;
                    background-color: $core-color; 
                }
            }
        }
    }
}