/* Configuration
============================= */

$sfull_has_overlay: false;
$sfull_animation_time: 1000ms;
$sfull_name: 'slider-breves';
.#{$sfull_name}{
    &-main,
    &-thumbnail, 
    &-slideNpop{
        position: relative;

        /* ITEM
        ============================= */
        .owl-item.active + .owl-item.active{
        }
        &__slider-item {
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: center;
            @include RWD(mobile){
                display: block;
            }
            &:before{ 
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                height: 100%;
                width: 1px;
                display: block;
                background-color: #505d6d;
                transform: translateY(-50%);
                @include RWD(mobile){
                    display: none;
                }
            }
        }
        &__slide{
            width: 520px;
            display: flex;
            align-items: flex-start;
            text-decoration: none;
            max-width: calc(50% - 80px);

            @include RWD(tablet){
                max-width: calc(50% - 40px);
            }
            @include RWD(mobile){
                width: 100%;
                flex-direction: column;
                align-items: center;
                max-width: 100%;
                & > * {
                    max-width: 100%;
                    text-align: center;
                }
                & + &{
                    margin-top: 20px;
                }
            }

            @if ($sfull_has_overlay) {
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 2;
                    height: 100%;
                    width: 100%;
                    background-color: rgba(#000000, 0.5);
                }
            }
            
            &:hover &-title,
            &:focus &-title{
                text-decoration: underline;
            }

            &-icon{
                height: 60px;
                width: 70px;
                margin-right: 30px;
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                flex-shrink: 0;
                @include RWD(mobile){
                    margin: 0;
                    margin-bottom: 15px;                    
                }
                
            }
            &-title{
                font-family: $main-font;
                font-size: 1.9rem;
                font-weight: 700;
                line-height: 1.2em;
                color: $core-color-font;
                text-transform: uppercase;

            }
            &-description{
                font-size: 1.7rem;
                font-weight: 400;
                line-height: 1.2em;
                color: #b6bcc3;
            }
            &--video {
                @if($sfull_has_overlay) {
                    .owl-video-wrapper {
                        &:before {
                            content: '';
                            position: absolute;
                            top: 0;
                            left: 0;
                            z-index: 2;
                            height: 100%;
                            width: 100%;
                            background-color: rgba(#000000, 0.5);
                        }
                    }
                }
                .owl-video-wrapper{
                    position: absolute;
                    top: 0; left: 0;
                    height: 100%;
                    width: 100%;
                }
                &:before {
                    display: none;
                }
                .owl-video-play-icon {
                    z-index: 3;
                    background-image: url(svg-bg-uri($svg-player, #FFFFFF));
                }
            }
        }

        /* ARROWS
        ============================= */

        &__arrow {
            position: absolute;
            top: 50%;
            z-index: 20;
            overflow: visible;
            transform: translateY(-50%);

            &.disabled{
                display: none !important;
            }

            .flexbox {
                position: relative;
                height: 50px;
                width: 50px;
                overflow: visible;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $core-color;
                @include RWD(mobile){
                    height: 30px;
                    width: 30px;
                }
            }
            &-icon {
                content: '';
                height: 20px;
                width: 20px;
                display: inline-block;
                background-image: url(svg-bg-uri($svg-map-chevron, $core-color-font));
                background-position: top center;
                background-size: contain;
                background-repeat: no-repeat;
                flex-shrink: 0;
                @include RWD(mobile){
                    height: 12px;
                    width: 12px;
                }
            }
            &--prev &-icon {
                transform: rotate(-180deg);
            }
            &-pagination {
                position: absolute;
                top: 0;
                height: 100%;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $core-color-font;
                color: $core-color;
                opacity: 0;
                transition: opacity .3s;
                @include RWD(mobile){
                    display: none !important;
                }
                &:after {
                    content: '/';
                    position: absolute;
                    top: 50%; left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            &--prev {
                left: 0;
            }
            &--prev &-pagination {
                right: 0;
                transform: translateX(100%);
            }
            &--next {
                right: 0;
            }
            &--next &-pagination {
                left: 0;
                transform: translateX(-100%);
            }
            &--total {}
            &:hover &-pagination, &:focus &-pagination{
                opacity: 1;
            }
        }

        /* Pager
        ============================= */

        &__pager {
            font-size: 1.7rem;
            font-weight: 700;
            &--current {
                display: none;
            }
            &--total--slideNpop{
                display: none;
            }
            &--prev {
                
            }
            &--next {
            }
            &--next, &--prev{
                position: absolute;
                top: 10px;
                left: 10px;
            }
            &--total{
                position: absolute;
                right: 10px;
                bottom: 10px;
            }
        }

        /* DOTS
        ============================= */

        &__dots {
            width: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            margin-top: 70px;
        }
        &__dot {
            height: 20px;
            width: 20px;
            background-color: $core-color-dark;
            font-size: 0; // Fait disparatre les chiffres dans les cercles
            border-radius: 50%;
            border: solid 2px $core-color;
            &.active {
                background-color: #FFFFFF;
            }
            & + & {
                margin-left: 5px;
            }
        }

        /* Playpause
        ============================= */
        
        &__playpause {
            content: '';
            position: absolute;
            right: 40px;
            bottom: 40px;
            z-index: 2;
            height: 20px;
            width: 20px;
            display: inline-block;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            flex-shrink: 0;
            &.play {
                background-image: url(svg-bg-uri($svg-play, #FFFFFF));
            }
            &.pause {
                background-image: url(svg-bg-uri($svg-pause, #FFFFFF));
            }
        }

        /* Slider Text
        ============================= */

        &__text {
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 2;
            color: #FFFFFF;
            transform: translate(-50%, -50%);
            transition: all 0.5s;
            max-width: calc(100% - 80px);
        }
        &__title {}
        &__description {}
        &__link {
            color: #FFFFFF;
        }

        /* Animation, loader et diffrents tats
        ============================= */

        /* -- Loader -- */
        &:not(.loaded){
            overflow: hidden;
            max-height: 400px;
        }
        .loader{
            position: absolute;
            top: 0;
            left: 0;
            z-index: 30;
            height: 100%;
            width: 100%;
            background-color: $core-color;       
        }
        &.loaded .loader{
            display: none !important;
        }

        /* -- Slides -- */

        .owl-item {
            @keyframes customIn {
                0% {
                    transform: translateX(100px);
                    opacity: 0;
                }
                100% {
                    transform: translateX(0);
                    opacity: 1;
                }
            }
            @keyframes customOut {
                0% {
                    opacity: 1;
                }
                100% {
                    opacity: 0;
                }
            }
            &.active {}
            &.animated {
                animation-duration: 1s;
            }
            &.customIn {
                animation-name: customIn;
            }
            &.customOut {
                animation-name: customOut;
            }
        }

        /* -- Texte -- */

        &.animating &__text {
            transform: translate(-50%, -50%) translateY(-40px);
            opacity: 0;
        }

        /* Elements disabled
        ============================= */
        
        &.no-autoplay &__playpause{display: none !important;}
        &.no-text &__text{display: none !important;}
        &.no-dot &__dots{display: none !important;}
        &.no-pager &__arrow-pagination{display: none !important;}
        &.no-arrow &__arrow{display: none !important;}
    }
    
}