.a-btn{
    /* Main
    ============================= */
    
        &-main{
            position: relative;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            padding: 15px 20px;
            cursor: pointer;
            transition: all 0.3s $t-bam;
            min-height: 40px;
            text-decoration: none !important;
            font-size: #{$browser-context / 10}rem;
            @include RWD(mobile){
                font-size: #{$browser-context-mobile / 10}rem;
            }

            
        
            .btn-text{
                position: relative;
                font-weight: 500;
                transition: all 0.3s $t-bam;
                text-transform: uppercase;
                font-family: $fancy-font;
                font-size: 1.4rem;
                font-weight: 400;
                line-height: 1;
            }
            i{
                font-size: 1.6rem;
                transition: all .3s $t-bam;
                flex-shrink: 0;
            }
            &:before{
                content: '';
                height: 0;
                width: 100%;
                position: absolute;
                bottom: 0;
                left: 0;
                z-index: 1;
                transition: .3s;
            }
            .flexbox{
                z-index: 2;
                width: 100%;
                display: flex;
                display: inline-flex;
                justify-content: center;
                align-items: center;
                align-items: center;
            }
            &:hover, &:focus{
                &:before{
                    height: 100%;
                }
            }
            &.icon-left{i{margin-right: 20px;}}
            &.icon-right{i{ margin-left: 20px;}}
            &:after{
                display: none !important; // Enlve l'external link
            }

            /* -- Skin -- */
            
            @mixin main-skin($bg-color, $text-color){
                background-color: $bg-color;
                .btn-text, i{
                    color: $text-color;
                }
            }
            
            @mixin main-skin-hover($bg-color, $text-color){
                &:before{
                    background-color: $bg-color
                }
                .btn-text, i{
                    color: $text-color;
                }
            }

            
            &.core{@include main-skin($core-color, $core-color-font);}
            &.core-inverted{
                @include main-skin($core-color-font, $core-color);
            }
            &.second{@include main-skin($second-color, $second-color-font);}
            &.second-inverted{@include main-skin($second-color-font, $second-color);}
            &.transparent{
                border-bottom: solid 1px #ffffff;
                @include main-skin(transparent, #ffffff);
                &:not(.h-transparent):hover, &:not(.h-transparent):focus{
                    border-color: transparent;
                }
            }
            &.transparent-dark{
                border-bottom: solid 1px #7e7e7e;
                @include main-skin(transparent, #7e7e7e);
                &:not(.h-transparent):hover, &:not(.h-transparent):focus{
                    border-color: transparent;
                }
            }
            &.transparent-core{
                border-bottom: solid 1px $core-color;
                @include main-skin(transparent, $core-color);
                &:not(.h-transparent):hover, &:not(.h-transparent):focus{
                    border-color: transparent;
                }
            }
            &.transparent-core-dark{
                border-bottom: solid 1px #212e3e;
                @include main-skin(transparent, #212e3e);
                &:not(.h-transparent):hover, &:not(.h-transparent):focus{
                    border-color: transparent;
                }
            }
            
            &.core.h-inverted{&:hover, &:focus{@include main-skin-hover($core-color-font, $core-color);}}
            &.core-inverted.h-inverted{&:hover, &:focus{@include main-skin-hover($core-color, $core-color-font);}}
            &.second.h-inverted{&:hover, &:focus{@include main-skin-hover($second-color-font, $second-color);}}
            &.second-inverted.h-inverted{&:hover, &:focus{@include main-skin-hover($second-color, $second-color-font);}}
            &.transparent.h-inverted{&:hover, &:focus{@include main-skin-hover(#FFFFFF, $core-color);}}
            &.transparent-dark.h-inverted{&:hover, &:focus{@include main-skin-hover(#7e7e7e, #FFFFFF);}}
            &.transparent-core.h-inverted{&:hover, &:focus{@include main-skin-hover($core-color, #FFFFFF);}}
            &.transparent-core-dark.h-inverted{&:hover, &:focus{@include main-skin-hover(#212e3e, #FFFFFF);}}
        }

    /* Link
    ============================= */
    
        &-link{
            position: relative;
            height: auto;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            padding: 10px;
            padding-right: 0;
            background-color: transparent;
            font-weight: bold;
            color: $core-color;
            transition: all 0.3s $t-bam;
            text-decoration: none !important;
            font-size: #{$browser-context / 10}rem;
            @include RWD(mobile){
                font-size: #{$browser-context-mobile / 10}rem;
            }
        
            .btn-text{
                position: relative;
                font-weight: 500;
                text-decoration: underline;
                transition: all .3s $t-bam;
            }
            i{
                font-size: 1.6rem;
                transition: all .3s $t-bam;
                flex-shrink: 0;
                background: none !important;
            }
            .flexbox{
                z-index: 1;
                width: 100%;
                display: inline-flex;
                justify-content: center;
                align-items: center;
            }
            &.icon-left{i{margin-right: 10px;}}
            &.icon-right{i{ margin-left: 10px;}}

            &.opened{
                i{
                    transform: rotate(90deg);
                }
            }
            @mixin link-skin($text-color){
                .btn-text, i{
                    color: $text-color;
                }
            }
            &.core{@include link-skin($core-color)}
            &.second{@include link-skin($second-color)}
            &.transparent{@include link-skin($text-color)}
            &.h-core{&:hover, &:focus{@include link-skin($core-color)}}
            &.h-second{&:hover, &:focus{@include link-skin($second-color)}}
            &.h-transparent{&:hover, &:focus{@include link-skin($text-color)}}
        }

    /* Icon circled
    ============================= */
    
        &-icon-circled{
            i{
                order: 1;
                height: 1.7em;
                width: 1.7em;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 1.2rem;
                border-radius: 50%;
            }
            .btn-text{
                order: 2;
            }
            @mixin icon-circled-skin($bg-color){
                @if $bg-color == white or $bg-color == #FFFFFF or $bg-color == #ffffff{ // Si bg blanc, on rajoute une darken au lieu de lighten
                    $bg-color: darken($bg-color, 8%);
                }
                background-color: $bg-color;
            }
            &.core{i{@include icon-circled-skin(lighten($core-color, 8%));}}
            &.second{i{@include icon-circled-skin(lighten($second-color, 8%));}}
            &.transparent{i{border-color: #FFFFFF;}}
            &.core.h-inverted{&:hover i, &:focus i{@include icon-circled-skin(lighten($core-color-font, 8%));}}
            &.second.h-inverted{&:hover i, &:focus i{@include icon-circled-skin(lighten($second-color-font, 8%));}}
            &.h-core{&:hover i, &:focus i{@include icon-circled-skin(lighten($core-color, 8%));}}
            &.h-second{&:hover i, &:focus i{@include icon-circled-skin(lighten($second-color, 8%));}}
            &.h-transparent{&:hover i, &:focus i{@include icon-circled-skin(transparent);}}
        }
}
.icon-left{
    i{
        order: 1;
    }
    .btn-text{
        order: 2;
    } 
} 
.icon-right{
    i{
        order: 2;
    }
    .btn-text{
        order: 1;
    }
}