.m-generic-form{

    .a-disclaimer{
        margin-bottom: em($gutter-y);
    }

    .form__group{
        display: block;
        flex-wrap: wrap;
        align-items: flex-start;
        margin-bottom: em($gutter-y);
        &:last-child{
            margin-bottom: 0;
        }
        &.cnil{
            margin-top: em(20);
        }
        &.webform-flexbox{
            display: flex;
        }
    }

    .form__label{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-bottom: em(10);

        &.visually-hidden{
            margin: 0;
        }
    }
    .form__field{
        display: flex;
        flex-direction: column;
        position: relative;
    }
    .form__item{
        & + .form__item{
            margin-top: em(15);
        }
    }
    
    .a-form-infos{
        width: 100%;
        margin-top: em(5);
    }

    // Inputs  la suite
    &:not(.m-generic-form--sbs) .form__inline-box{
        display: flex;
        margin-bottom: em($gutter-y);

        .form__group{
            width: 100%;
            margin-bottom: 0;
            & + .form__group{
                margin-left: em(20);
            }
        }

        @include RWD(mobile){
            display: block;
            .form__group{
                width: 100%;
                margin-bottom: em($gutter-y);

                & + .form__group{
                    margin-left: 0;
                }
            }
        }
    }

    // Version drupal
    .webform-flexbox{
        margin-bottom: em($gutter-y);
        margin-top: 0;

        &:last-child{
            margin-bottom: 0;
        }
    }

    .form__actions{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-top: em($gutter-y);

        > * {
            margin-left: em(20);
            &.previous{
                margin-left: 0;
                margin-right: auto;
            }
        }

        @include RWD(mobile){
            flex-direction: column;
            align-items: flex-end;

            & > *{
                margin-left: 0;

                & + * {
                    margin-top: em($gutter-y);
                }
            }
        }
    }

    .form__footer{
        margin-top: em(30);
        
        &:before{
            display: block;
            content: ''; 
            margin-left: auto;
            margin-right: auto;
            margin-bottom: em(30);
            height: 1px;
            background-color: #d8d8d8;
            width: 80%;
        }
    }


    @include RWD(mobile){
        .form__group{
            display: block;
        }
        .form__label{
            width: 100%;
            margin-right: 0;
            margin-bottom: #{em($gutter-y) / 2};
            display: block;
        }
        .form__field{
            width: 100%;
            margin-left: 0;
        }
    }

    // Validation    
    .error{
        border-color: $error-color !important;
    }
    span.error{
        color: $error-color;
        margin-top: em(5);
    }
    .valid{
        border-color: $success-color !important;
    }
    &--sbs{
        .form__inline-box{
            margin-bottom: em($gutter-y);
        }
        @include RWD(tablet_desktop){
            legend{
                width: 100% !important;
                margin-bottom: em($gutter-y) !important;
            }
            .webform-flexbox{
                display: block;

                .form__group{
                    margin-bottom: em($gutter-y);
                }
            }
            .form__group{
                display: flex;
                &--radio, &--checkbox{
                    .form__field{            
                        padding : em(15) em(20) em(0) em(20);
                        flex-flow : row wrap;
                        background-color: #F4F4F4;
                        border-radius : 5px;
                    }
                    .form__item{
                        margin-right: em(20);
                        margin-top: 0;
                        &:last-child{
                            margin-right: 0;
                        }
                    }
                    .form__item{
                        margin-bottom: em(15);
                    }
                    .error{
                        width: 100%;
                        margin-top: em(12);
                    }
                }
            }
            .form__label{
                width: 20%;
                padding : em(2) em(20) em(5) 0;
                margin-bottom: 0;
                flex-shrink: 0;
                justify-content: flex-end;
            }
            .a-label{
                text-align: right;
            }
            .a-form-infos{
                padding-left: 20%;
            }
            .a-fancy-file{
                .a-form-infos{
                    padding-left: 0;
                }
            }
            .form__field:not(.fieldset-wrapper){
                width : 80%;
                flex-shrink: 0;
                .form__field{
                    width: 100%;
                }
            }   
            .form__field.fieldset-wrapper{
                width: 100%;
                .form__field{
                    width: 80%;
                    flex-shrink: 0;
                }
            }         
        }
    }
    .js-form-item {
        &[class*="other"] {
            .form__field {
                display: block;
            }
        }
    }
}