.hp-rencontres{
    @include RWD(tablet){
        margin-bottom: 100px;
    }
    &__title{
        position: relative;
        margin-bottom: 130px;
        @include RWD(tablet){
            margin-bottom: 50px;
        }
        @include RWD(mobile){
            font-size: 3rem;
            text-align: center;
            align-items: center;
            margin-bottom: 30px;
        }
    }
    &__list{
        width: 100%;
        display: flex;
        align-items: center;

        @include RWD(mobile){
            display: block;
        }
    }
    &__card{
        background-color: #FFFFFF;
        position: relative;
        @include RWD(mobile){
            width: 280px;
            margin: 0 auto;
        }


        &:first-child{
            width: 53.34%;
            transform: translateY(-85px);

            @include RWD(tablet){
                transform: translateY(-45px);
            }

            @include RWD(mobile){
                width: 100%;
                transform: none !important;
                top: 0 !important;
            }
        }
        &:last-child{
            flex: 1;
            transform: translateX(-50px);

            @include RWD(tablet){
                width: 100%;
                transform: none !important;
                top: 0 !important;
                margin-top: 60px;
            }
        }
    }
    &__card-picture{
        height: 520px;
        width: 100%;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.2);

        @include RWD(mobile){
            height: 380px;
            width: 100%;
        }
    }
    &__card-text{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px;
        @include RWD(mobile){
            padding: 20px;
        }

        & > * {
            text-align: center;
            max-width: 100%;
        }
    }
    &__card:first-child &__card-text{
        padding: 30px 70px 30px 20px;
        @include RWD(mobile){
            padding: 20px;
        }
    }
    &__card-title{
        margin-bottom: 15px;
        font-family: $fancy-font;
        font-size: 3.4rem;
        font-weight: bold;
        line-height: 1.2em;
        text-align: center;
        color: $second-color-dark;

        @include RWD(mobile){
            font-size: 2.4rem;
        }
    }
    &__card-lead{
        margin-bottom: 15px;
        font-family: $main-font;
        font-size: 1.7rem;
        line-height: 1.2em;
        color: #7e7e7e;
        @include RWD(mobile){
            font-size: 1.5rem;
        }
    }
    &__card-link{}
    &__card-more{
        margin-top: 15px;
    }
}