.smag{
    .portrait{
        &__card{
            position: relative;
            background-color: #FFFFFF;
        }
        &__card-picture{
            width: 100%;
            padding-bottom: #{520% * 100 / 385};
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.2);
    
            // @include RWD(mobile){
            //     height: 380px;
            //     width: 100%;
            // }
        }
        &__card-text{
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 30px;
            @include RWD(mobile){
                padding: 20px;
            }
    
            & > * {
                text-align: center;
                max-width: 100%;
            }
        }
        &__card-title{
            margin-bottom: 25px;
            font-family: $fancy-font;
            font-size: 3.4rem;
            font-weight: bold;
            line-height: 1.2em;
            text-align: center;
            color: $second-color-dark;
    
            @include RWD(mobile){
                font-size: 2.4rem;
            }
        }
        &__card-lead{
            margin-bottom: 20px;
            font-family: $main-font;
            font-size: 1.7rem;
            line-height: 1.2em;
            color: #7e7e7e;
            @include RWD(mobile){
                font-size: 1.5rem;
            }
        }
        &__card-link{}
        &__card-more{
            margin-top: 15px;
        }
    }

    // Highlighted
    .portrait__card.highlighted{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        .portrait__card-picture{
            width: 440px;
            padding-bottom: #{600% * 100 / 600};
            max-width: calc(100% - 20px);
        }
        .portrait__card-text{
            position: relative;
            z-index: 2;
            width: 345px;
            display: block;
            background-color: #FFFFFF;
            transform: translateY(-170px);
            margin-bottom: -170px;
            align-self: flex-end;
            max-width: calc(100% - 20px);

            @include RWD(mobile){
                transform: translateY(-80px);
                margin-bottom: -80px;                
            }
        }
        .portrait__card-title{
            text-align: left;
            &:before{
                content: '';
                height: 13px;
                width: 50px;
                display: inline-block;
                display: block;
                margin-bottom: 5px;
                background-image: url( svg-bg-uri($svg-wave, #e7e7e7) );
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                flex-shrink: 0;
            }
        }
        .portrait__card-lead{
            text-align: left;
        }
        .portrait__card-link{            
            text-align: left;
        }
        .portrait__card-more{         
            text-align: left;
        }
    }
}