.smag-container{
    width: 100%;
    display: block;
    padding-left: 80px;
    padding-right: 80px;
    margin-left: auto;
    margin-right: auto;

    @include RWD(tablet){
        width: 100%;
        padding-left: 40px;
        padding-right: 40px;
    }
    @include RWD(mobile){
        width: 100%;
        padding-left: 20px;
        padding-right: 20px;
    }
}
.smag-container-form{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: 920px;
}