// recup strasbourg
%atom-text-menu-item{
    text-transform: uppercase;
    font-family: $fancy-font;
    font-weight: 700;
    font-size: 1.7rem;
    color: #FFFFFF;
    text-shadow: none;
    transition: all, 0.3s;
}
.smag-header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    z-index: 200;
    transform: translate3d(0,0,0);
    transition: all 0.3s;
    @include RWD(tablet){
        position: fixed;
    }
    a{
        text-decoration: none;
    }
    &.smag-mmenu{
        height: 100%;
    }
    .smag-scrolled-search-engine{
        display: none;
        background-color: #f6f6f6;
        height: 72px;
    
        .smag-search{
            @extend .container;
            display: flex;
            align-items: center;
            height: 100%;
            ::-webkit-input-placeholder {
                text-transform: uppercase;
            }
            :-moz-placeholder{ 
                text-transform: uppercase;
            } 
            ::-moz-placeholder { 
                text-transform: uppercase;
            }
            :-ms-input-placeholder {  
                text-transform: uppercase;
            }
            input{
                border: none;
                border-radius: 5px;
                padding: 20px;
                background-color: transparent;
                color: #7e7e7e;
                width: 520px;
                max-width: 100%;
                font-family: $fancy-font;
                font-size: 1.4rem;
                font-weight: 400;
                flex: 1;
                @include RWD(mobile){
                    padding: 15px 40px 15px 15px;
                    font-size: 1.2rem;
                }
            }
    
            [type="submit"]{
                content: '';
                display: inline-block;
                width: 20px;
                height: 20px;
                background-size: 20px;
                background-position: center;
                background-repeat: no-repeat;
                background-image: url( svg-bg-uri($svg-loupe, #7d7d7d) );
                @include RWD(mobile){
                    width: 35px;
                }
            }
            .smag-fill{
                flex: 1;
            }
            .smag-search-close{
                content: '';
                display: inline-block;
                width: 20px;
                height: 20px;
                background-size: 20px;
                background-position: center;
                background-repeat: no-repeat;
                background-image: url( svg-bg-uri($svg-cross, #7d7d7d) );
            }
        }
    }
    // Nav top
    .smag-nav-top{
        height: 45px;
        background: #FFFFFF;
        z-index: 2;
        width: 100%;
        position: relative;
        transition: all 0.3s;
        .smag-container{
            display: flex;
            align-items: center;
            height: 100%;

            @include RWD(tablet){
                padding: 0;
            }
        }
        .smag-menu-triggerBox{
            display: none;
            @include RWD(tablet){
                display: block;
            }
            // Bouton Menu RWD
            #smag-menu-trigger{
                margin-left: 20px;
                position: absolute;
                visibility: hidden;
        
                & + label{             
                    position: relative; 
                    margin-left: 10px;
                    margin-right: 10px;   
                    z-index: 10;  
                    transition: all, 0.5s; 
        
                    &, &:before, &:after{
                        content: '';
                        display: inline-block;
                        width: 20px;
                        height: 18px;
                        background-size: 20px;
                        background-position: center;
                        background-repeat: no-repeat;
                        background-image: url( svg-bg-uri($svg-bar, #7d7d7d) );
                    }
                    &:before{
                        position: absolute;
                        top: 0;
                        left: 0;
                        background-position: center top 0px;
                        transition: all, 0.3s;
                    }
                    &:after{
                        position: absolute;
                        top: 0; left: 0;
                        background-position: center bottom 0px;
                        transition: all, 0.3s;
                    }
                }
                &:checked{
                    & + label{
                        transform: translateX(320px) translateX(-100%) translateX(-20px);
                        background-position: left -50px center;
                        background-image: url( svg-bg-uri($svg-bar, #FFFFFF) );
                        &, &:before, &:after{
                            width: 25px;
                            height: 25px;
                            background-size: 25px;
                        }
                        &:before{
                            background-position: center;
                            transform: rotate(-45deg);
                            background-image: url( svg-bg-uri($svg-bar, #FFFFFF) );
                        }
                        &:after{
                            background-position: center;
                            transform: rotate(45deg);
                            background-image: url( svg-bg-uri($svg-bar, #FFFFFF) );
                        }
                    }
                    & ~ .smag-menu-panel{
                        transform: translateX(0);
                        .smag-menu-item-container a{
                            transform: translateX(0);
                        }
                    }
                    & ~ .smag-menu-rwd-overlay{
                        display: block;
                    }
                }
            }
            // Panel Menu RWD
            .smag-menu-panel{
                position: fixed;
                top: 0;
                left: 0;
                height: 100vh;
                width: 320px;
                background: $core-color;
                transform: translateX(-100%);
                transition: all, 0.5s;
                z-index: 9;
                display: flex;
                flex-direction: column;
        
                .smag-menu-rwd-logo{
                    width: 135px;
                    margin-top: 10px; margin-left: 20px;
                }
                .smag-menu-rwd-content{
                    padding: 40px;
                    padding-top: 60px;
                    padding-bottom: 0;
                    height: calc(100% - 42px);
                    // overflow: auto;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
        
                    @include RWD(mobile){
                        padding-top: 30px;
                    }
        
                    .smag-container{
                        display: block;
                        margin-bottom: 20px;
                        position: relative;
                        overflow: auto;
                    }
                    .smag-menu-item-container{                
                        & > a{
                            transform: translateX(-150%);
                        }
                        @for $i from 1 to 10{
                            &:nth-child(#{$i}) a{
                                transition: transform 0.5s #{($i * 0.1)-0.1}s;
                            }
                        }
                    }
                    .smag-menu-item{
                        @extend %atom-text-menu-item;
                        display: block;
                        padding: 15px 0;
                        position: relative;
                        padding-right: 40px;
        
                        &:after{
                            content: '';
                            display: inline-block;
                            width: 20px;
                            height: 20px;
                            background-size: 20px;
                            background-position: center;
                            background-repeat: no-repeat;
                            background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
                            position: absolute;
                            right: 0;
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                    .smag-menu-subitem{
                        display: block;
                        position: absolute;
                        top: 0; left: 0; bottom: 0; width: 100%;
                        background-color: $core-color;
                        // padding: 20px 40px 40px 40px;
                        transform: translateX(-100%) translateX(-40px);
                        transition: all, 0.5s;
                        font-family: $fancy-font;
                        font-size: 1.5rem;
                        font-weight: 700;
                        color: #FFFFFF;
                        text-transform: uppercase;
                        z-index: 100;
        
                        &.opened{
                            transform: translateX(0);
                        }
        
                        // .smag-container{
                        //     display: flex;
                        //     flex-direction: column;
                        //     align-items: flex-start;
        
                        // }
                        // .smag-submenu-grid{
                        //     flex: 1;
                        // }
                        .smag-submenu-quicklinks{
                            display: none;
                        }
                        .smag-submenu-item{
                            margin-bottom: 15px;
                        }
                        .smag-rwd-back{
                            margin-bottom: 30px;
                            display: flex;
                            align-items: center;
                            &:before{
                                content: '';
                                direction: block;
                                width: 15px;
                                height: 15px;
                                background-size: 15px;
                                background-position: left center;
                                background-repeat: no-repeat;
                                background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
                                transform: rotate(180deg);
                                margin-right: 10px;
                            }
                        }
                        .smag-title{
                            padding: 10px 0;
                            letter-spacing: 0.07em;
                            font-size: 1.3rem;
                            text-transform: none;
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            &:after{
                                content: '';
                                display: block;
                                width: 18px;
                                height: 15px;
                                background-size: 15px;
                                background-position: left center;
                                background-repeat: no-repeat;
                                background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
                                margin-right: 10px;
                                transform: rotate(90deg);
                                flex-shrink: 0;
                                margin-left: 10px;
                            }
                        }
                        .smag-links{
                            display: none;
                        }
                        .smag-link{
                            font-weight: 500;
                            font-size: 1.2rem;
                            text-transform: none;
                            color: #FFFFFF;
                            display: block;
                            cursor: pointer;
                            padding-top: 10px;
                            padding-bottom: 10px;
                            padding-left: 25px;
                            // background-size: 15px;
                            // background-position: left center;
                            // background-repeat: no-repeat;
                            // background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
                            letter-spacing: 0.05em;
                            position: relative;
                            &:after{
                                @extend %animation-hoverBar-in;
                                width: calc(100% - 25px);
                                background-color: #FFFFFF;
                                transform: translateY(10px);
                            }
                            &:hover, &:focus{
                                text-decoration: none;
                                &:after{
                                    @extend %animation-hoverBar-out;
                                }
                            }
                        }
                    }
                }
                .smag-nav-contact{
                    margin: 0 -40px;
                    padding: 40px;
                    display: block;
                    background-color: #FFFFFF;
                    height: unset;
                    .smag-picto{
                        margin-right: 10px;
                    }
                    .smag-text{
                        display: block;
                    }
                    @include RWD(mobile){
                        padding: 10px 40px;
                    }
                }
            }
            // Overlay gris
            .smag-menu-rwd-overlay{
                content: '';
                display: none;
                position: fixed;
                top: 0; left: 0;
                width: 100%; height: 100%;
                z-index: 5;
                background: rgba(0, 0, 0, 0.7);
            }
        }
        .smag-scrolled-logo{
            height: auto;
            width: 195px;
            @include RWD(tablet){
                margin-left: 10px;
                width: 160px;
            }
            @include RWD(mobile){
                margin-left: 0;
                display: block;
                width: 150px;
                height: auto;
            }
        }
        .smag-fill{
            flex: 1;
        }
        .smag-nav-btn{
            height: 100%;
            padding: 0 25px;
            font-size: 1.7rem;
            position: relative;
            @include RWD(mobile){
                padding: 0 10px;
            }

            button{
                height: 100%;
            }

            .smag-flexbox{
                display: flex;
                align-items: center;
                height: 100%;
            }
            .smag-text{
                line-height: 1;
                color: #505050;
                font-family: $fancy-font;
                font-weight: 700;
                font-size: 1.7rem;
                @include RWD(mobile){
                    display: none;
                }
                position: relative;
                &:after{
                    @extend %animation-hoverBar-in;
                    background-color: #505050;
                }
            }
            .smag-picto{
                position: relative;
                content: '';
                display: inline-block;
                width: 20px;
                height: 20px;
                background-size: 20px;
                background-position: center;
                background-repeat: no-repeat;
                margin-right: 10px;
                @include RWD(mobile){
                    margin: 0;
                } 
            }
            .notif-amount{
                background-color: $third-color;
                position: absolute;
                top: -7px;
                right: -7px;
                width: auto;
                padding: 0 3px;
                height: 18px;
                color: #fff;
                border-radius: 50%;
                font-size: 1.1rem;
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: $main-font;
                font-weight: 700;
                font-size: 1.1rem;
                color: $core-color;
            }
            .smag-arrow{
                content: '';
                display: inline-block;
                width: 17px;
                height: 17px;
                background-size: contain;
                background-position: center;
                background-repeat: no-repeat;
                margin-right: 10px;
                background-image: url( svg-bg-uri($svg-arrow1, #505050) ); 
                transform: rotate(90deg);
                margin-left: 20px; 
                transition: all, 0.5s;
                @include RWD(mobile){
                    margin-left: 5px;
                    margin-right: 5px;
                }
            }
            &:hover, &:focus{
                text-decoration: none;
                .smag-text{
                    &:after{
                        @extend %animation-hoverBar-out;
                    }
                }
                .smag-arrow{
                    transform:  translateY(3px) rotate(90deg);
                }
                #account-menu{
                    // display: block;
                }
            }

            &.smag-nav-account{
                background-color: $third-color;
                .smag-picto{
                    background-image: url( svg-bg-uri($svg-account, #505050) );                    
                }
            }
            &.smag-nav-contact{
                .smag-picto{
                    background-image: url( svg-bg-uri($svg-mail, #505050) );
                }
            }
            #account-menu{
                position: absolute;
                top: 100%;
                left: 50%;
                transform: translateX(-50%);
                width: 100%;
                min-width: 250px;
                background-color: #FFFFFF;
                padding: 15px 15px 25px 15px;
                border-bottom-left-radius: 7px;
                border-bottom-right-radius: 7px;
                display: none;

                .account-link{
                    display: flex;
                    align-items: center;
                    font-size: 1.5rem;
                    color: #505050;
                    font-weight: 400;
                    font-family: $main-font;
                    letter-spacing: 0.03em;
                    &:before{
                        content: '';
                        display: inline-block;
                        width: 23px;
                        height: 23px;
                        background-size: contain;
                        background-position: center;
                        background-repeat: no-repeat;
                        margin-right: 15px;
                    }
                    &.dashboard:before{
                        background-image: url( svg-bg-uri($svg-dashboard, #505050) );
                    }
                    &.account:before{
                        background-image: url( svg-bg-uri($svg-account, #505050) );
                    }
                    &.notification:before{
                        background-image: url( svg-bg-uri($svg-notification, #505050) );
                    }
                    &.logout:before{
                        background-image: url( svg-bg-uri($svg-close, #505050) );
                    }
                }
                li + li{
                    margin-top: 20px;
                    &:first-child{
                        margin-top: 0;
                    }
                }
            }
        }
        // Theme dark
        &.dark{
            background-color: #333333;
            .smag-nav-btn{
                &.smag-nav-account{
                    .smag-picto{
                        background-image: url( svg-bg-uri($svg-account, #ffffff) );                    
                    }
                }
                &.smag-nav-contact{
                    .smag-picto{
                        background-image: url( svg-bg-uri($svg-mail, #ffffff) );
                    }
                }
                .smag-text{
                    color: #FFFFFF;
                    &:after{
                        background-color: #ffffff;
                    }
                }
                .smag-arrow{
                    background-image: url( svg-bg-uri($svg-arrow1, #ffffff) ); 
                }
                #account-menu{
                    background-color: #333333;
                    .account-link{
                        color: #FFFFFF;
                        &.dashboard:before{
                            background-image: url( svg-bg-uri($svg-dashboard, #ffffff) );
                        }
                        &.account:before{
                            background-image: url( svg-bg-uri($svg-account, #ffffff) );
                        }
                        &.notification:before{
                            background-image: url( svg-bg-uri($svg-notification, #ffffff) );
                        }
                        &.logout:before{
                            background-image: url( svg-bg-uri($svg-close, #ffffff) );
                        }
                    }
                }

            }
        }
        #smag-search-trigger{
            display: none;
            background-color: $core-color;
            content: '';
            width: 55px;
            height: 100%;
            background-size: 15px;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url( svg-bg-uri($svg-loupe, #FFFFFF) );
            outline: none !important;

            @include RWD(tablet){
                display: block;
            }
        }
        .smag-lang-list{
            display: flex;
            height: 100%;

            @include RWD(tablet){
                display: block;
            }
            &:hover{
                @include RWD(desktop){
                    .smag-lang{
                        display: flex;
                    }                    
                }
            }
            &.smag-open{
                @include RWD(tablet){
                    .smag-lang{
                        display: flex;
                    }
                }
            }
        }
        .smag-lang{
            width: 55px;
            height: 100%;
            font-family: $fancy-font;
            font-size: 1.4rem;
            font-weight: 400;
            color: #7e7e7e;
            display: flex;
            align-items: center;
            justify-content: center;
            text-transform: uppercase;
            background-color: #FFFFFF;
            transition: all, 0.5s;
            @include RWD(tablet){
                display: none;
            }
            &.actif{
                background-color: $core-color;
                pointer-events: none;
                color: #FFFFFF;
                display: flex;
            }
            &:hover, &:focus{
                &:not(.actif){
                    background-color: #7e7e7e;
                    color: #FFFFFF;
                    text-decoration: none;
                }
            }
        }
    }
    // Nav bottom
    .smag-nav-bottom{
        height: 60px;
        background: $core-color; 
        z-index: 1;
        position: static;
        top: calc(100vh - 60px);
        // transform: translateY(-100%);
        transition: transform 0s;
        width: 100%; 
        // border-bottom: solid 1px #dcdcdc;
        box-shadow: 3px 4px 5px 0px rgba(0, 0, 0, 0.05);
        @include RWD(tablet){
            display: none;
        }

        .smag-container{
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: 100%;
            @include RWD(tablet){
                padding: 20px;
            }
            display: flex;

            .smag-menu-item-container{
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 0 10px;
                @include RWD(tablet){
                    // padding: 0;
                }
            }

            .smag-menu-item{
                @extend %atom-text-menu-item;
                // border-bottom: solid 4px transparent;
                height: 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                @include RWD(tablet){
                    font-size: 1.3rem;
                }
                &:hover, &:focus{
                    text-decoration: none;
                    opacity: 0.4;
                    // border-color: #FFFFFF;
                }
            }

            .smag-menu-subitem{
                position: absolute;
                top: 100%;
                left: 50%;
                width: 100vw;
                transform: translateX(-50%);
                background-color: $second-color;
                display: none;
                max-height: 80vh;
                overflow: auto;
                @include RWD(tablet){
                    display: none !important;
                }
                & > .smag-container{
                    height: auto;
                }
            }

            .smag-container{
                display: flex;
                align-items: stretch;
            }

            .smag-submenu-grid{
                width: 1200px;
                padding: 35px 0;
                margin: 0 auto;
                display: flex;
                flex-wrap: wrap;

                .smag-submenu-item{
                    margin-bottom: 35px;
                    padding-right: 25px;
                    font-family: $fancy-font;
                    font-size: 1.5rem;
                    font-weight: 700;
                    color: $core-color;
                    text-transform: uppercase;
                    width: 33.3%;

                    .smag-title{
                        margin-bottom: 10px;
                        letter-spacing: 0.07em;
                        font-size: 1.5rem;
                        font-weight: 700;
                        color: $core-color;
                        text-decoration: none;
                        display: block;
                    }
                    a.smag-title{     
                        position: relative;    
                        &:hover, &:focus{
                            text-decoration: underline;
                        }
                    }
                    .smag-links{
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                    }
                    .smag-link{
                        font-weight: 400;
                        font-size: 1.3rem;
                        text-transform: none;
                        color: $core-color;
                        display: inline-block;
                        cursor: pointer;
                        margin-bottom: 5px;
                        padding-left: 25px;
                        background-size: 15px;
                        background-position: left 8px;
                        background-repeat: no-repeat;
                        background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
                        letter-spacing: 0.05em;
                        position: relative;
                        line-height: 25px;
                        letter-spacing: 0.05em;
                        &:after{
                            @extend %animation-hoverBar-in;
                            width: calc(100% - 25px);
                            background-color: #FFFFFF;
                            transform: translateY(10px);
                        }
                        &:hover, &:focus{
                            text-decoration: none;
                            &:after{
                                @extend %animation-hoverBar-out;
                            }
                        }
                    }
                }
            }

            // Accs rapides
            .smag-submenu-quicklinks{
                width: 25%;
                background-color: $core-color;
                padding: 35px 25px;

                .smag-quicklink-title{
                    text-align: center;
                    font-family: $fancy-font;
                    font-weight: 700;
                    font-size: 1.7rem;
                    text-transform: uppercase;
                    line-height: 0.8;
                    color: #FFFFFF;
                    margin-bottom: 20px;

                    &:before{ 
                        content: '';
                        display: block;
                        height: 15px;
                        background-size: 50px; 
                        background-position: center;
                        background-repeat: no-repeat; 
                        background-image: url( svg-bg-uri($svg-wave, #505D6D) ); 
                        margin-bottom: 10px;
                    }
                }
                .smag-grid{
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-around;
                    align-items: center;
                }
                .smag-item{
                    width: 40%;
                    text-align: center;
                    margin-top: 25px;
                    transition: all, 0.3s;

                    &.smag-external{
                        .smag-title:after{
                            content: '';
                            display: inline-block;
                            width: 10px;
                            height: 10px;
                            background-size: 10px;
                            background-position: center;
                            background-repeat: no-repeat;
                            background-image: url( svg-bg-uri($svg-external, #FFFFFF) );
                            margin-left: 5px;
                            // transform: translateY(-5px);
                        }
                    }

                    &:nth-child(1),
                    &:nth-child(2){
                        margin-top: 0;
                    }

                    &:hover, &:focus{
                        text-decoration: none;
                        transform: scale(1.1);
                    }
                }
                .smag-title{
                    color: #FFFFFF;
                    font-family: $main-font;
                    font-weight: 500;
                    font-size: 1.4rem;
                    text-transform: uppercase;
                }
                .smag-visu{
                    background-color: #FFFFFF;
                    height: 70px;
                    width: 70px;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin: 0 auto;
                    margin-bottom: 10px;
                    img{
                        width: 50px;
                        height: auto;
                    }
                }
            }
        }
    }

    // Etats scrolled
    &.scrolled{
        .smag-nav-top{
            transform: translateY(-100%);   
            @include RWD(tablet){
                transform: none !important;
                position: fixed;
            }
        }
    }
    &.scrolled-hp{
        position: fixed;
        .smag-nav-top{
            transform: translateY(0);
            .smag-scrolled-logo{
                display: block;
            }
            #smag-search-trigger{
                display: block;
            }
        }
        .smag-nav-bottom{
            transition: transform 0.3s;
            position: relative;
            top: -45px !important;
            transform: translateY(45px);
        }
    }
}


// Surcharge smag
.smag-header:not(.scrolled){
    .smag-nav-top{
        background-color: transparent;
    }
    .smag-nav-top .smag-scrolled-logo{
        path, g{
            fill: #FFFFFF;
        }
    }
    .smag-nav-top .smag-nav-btn.smag-nav-account,
    .smag-nav-top .smag-lang.actif,
    .smag-nav-top .smag-lang{
        background-color: transparent;
    }
    .smag-nav-bottom{
        background-color: rgba(#2c2c2c, 0.5);
    }
    .smag-nav-top .smag-lang,
    .smag-nav-top .smag-lang.actif{
        color: #7e7e7e;
    }
    .smag-nav-top .smag-lang.actif,
    .smag-nav-top .smag-nav-btn .smag-text{
        color: #FFFFFF;
    }
    .smag-nav-top .smag-nav-btn.smag-nav-contact .smag-picto{
        background-image: url( svg-bg-uri($svg-mail, #ffffff) );                   
    }
    .smag-nav-top .smag-nav-btn.smag-nav-account .smag-picto{
        background-image: url( svg-bg-uri($svg-account, #ffffff) ); 
    }
}