%atom-home-slider-arrow{
    content: '';
    position: absolute;
    top: 0;
    z-index: 100;
    height: 100%;
    width: 60px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all, 0.5s;

    
    .smag-picto{
        position: relative;
        left: 15px;
        z-index: 10;
        height: 60px;
        width: 60px;
        background-color: $second-color;
        background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
        background-position: center;
        background-size: 30px;
        background-repeat: no-repeat;
        border-radius: 50%;
        @include RWD(tablet){
            height: 35px;
            width: 35px;
            background-size: 20px;
        }
        @include RWD(mobile){
            left: 35px;
            height: 40px;
            width: 40px;
            background-size: 18px;
        }
    }

    .flexbox{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &.owl-prev{
        transform: rotate(180deg);
    }

    &.disabled{
        opacity: 0 !important;
        pointer-events: none;
        @include RWD(mobile){
            display: none;
        }
    }

    &--grey{
        background-color: #202020;
        @extend %atom-home-slider-arrow;
        path{
            transition: all, 0.5s;
            fill: #FFFFFF;
        }
        &:hover, &:focus{
            background-color: $core-color;
        }
    }
}



#smag-socials{
    position: relative;
    &:before{
        content: '';
        position: absolute;
        top: 160px;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: #f6f6f6;

        @include RWD(mobile){
            top: 130px;
        }
    }
    & > * {
        position: relative;
        z-index: 3;
    }
    .smag-socials__title{
        margin-bottom: 42px;

        @include RWD(mobile){
            font-size: 3rem;
        }
    }
    .smag-filters{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 40px;

        .smag-social-filter{
            height: 40px; width: 40px;
            padding: 0px !important;
            margin: 0 5px;
            
            // Bouton texte
            &.smag-texted{
                // @extend %atom-btn-round--grey;
                background-color: #FFFFFF;
                border-radius: 10px;
                .smag-btn-text{
                    font-size: 1.0rem;
                    font-weight: 800;
                    color: $core-color;
                    text-transform: uppercase;
                }
                &.smag-actif{
                    background-color: $second-color;
                    border-color: $second-color;
                    pointer-events: none;
                    svg{
                        fill: $core-color; 
                    }          
                } 
                &:hover, &:focus{
                    background-color: $second-color !important;
                    .smag-btn-text{
                        color: $core-color !important;
                    }
                }   
            }
            // Boutons Picto
            &:not(.smag-texted){
                background-color: #ffffff;
                transition: all, 0.5s;
                border-radius: 10px;
                border-style: solid;
                border-width: 1px;
                border-color: transparent;
                border-color: #ffffff;
                
                svg{
                    height: 17px;
                    width: 17px;
                    transition: all, 0.5s;
                    fill: $core-color;
                }
                &:hover, &:focus{
                    background-color: $second-color;
                }
                &.smag-actif{
                    background-color: $second-color;
                    border-color: $second-color;
                    pointer-events: none;
                    svg{
                        fill: $core-color; 
                    }              
                }

            }
        }
    }
    // Slider
    .smag-slider-social-container{
        position: relative;
        position: relative;
        left: 50%;
        z-index: 2;
        width: 100vw;
        margin-bottom: 40px;
        transform: translateX(-50%) translateX(98px);
        @include RWD(desktop){
            min-height: 500px;
        }
        @include RWD(tablet){
            width: 100%;
            transform: translateX(-50%);
        }
        @include RWD(mobile){
            margin-bottom: 10px;
        }
        .smag-slider-overflow{
            overflow: hidden;
            padding: 10px;
            margin: -10px;
        }
        .smag-slider{
            width: calc(100vw - 200px);
            @include RWD(tablet){
                width: 80%;
                margin: 0 auto;
            }

            // Animate Out
            @for $i from 1 through 8{
                $delay: $i * 0.05;
                .owl-item.active{
                    .smag-item:nth-child(#{$i}){
                        transition: transform 0.3s #{$delay}s , opacity 0.3s #{$delay}s;
                    }
                }
            }

            .owl-stage-outer{
                overflow: visible !important;
            }

            // Taille et couleur des slides
            .owl-item{
                transition: opacity 1s 0.3s;
                &.active{
                    .smag-item{
                        opacity: 1;
                    }
                }
                .smag-page{
                    width: 100%;
                    &:not(.smag-big){
                        @include RWD(desktop){
                            @include grid-distribution(4, 20, 'smag-item');
                        }                    
                        @include RWD(tablet){
                            @include grid-distribution(2, 20, 'smag-item');
                        }
                        @include RWD(mobile){
                            @include grid-distribution(1, 0, 'smag-item');
                        }
                    }
                    &.smag-big{
                        @include RWD(desktop){
                            display: flex;
                            flex-wrap: wrap;
                            justify-content: space-between;
                            .smag-item{
                                width: calc(25% - ( (60px) / 4));
                                &.smag-big{
                                    width: calc(50% - ( (60px) / 6));
                                }
                            }                            
                        }         
                        @include RWD(tablet){
                            @include grid-distribution(2, 20, 'smag-item');
                        }
                        @include RWD(mobile){
                            @include grid-distribution(1, 0, 'smag-item');
                        }
                    }
                }
            } 
            &.animate-out{
                @for $i from 1 through 8{
                    $delay: (8 - $i) * 0.05;
                    .owl-item.active{
                        .smag-item:nth-child(#{$i}){
                            transition: transform 0.3s #{$delay}s , opacity 0.3s #{$delay}s;
                        }
                    }
                }
                .owl-item{
                    &:not(.active){
                        opacity: 0;
                        transition: opacity 0.3s;
                    }
                    &.active{
                        .smag-item{
                            transform: translate(0, 40px);
                            opacity: 0;
                        }
                    }
                } 
            }
        }

        // NEXT/PREV ARROWS
        .owl-nav{
            z-index: 10;

            @include RWD(mobile){
                // display: none;
            }

            .smag-owl-prev{
                left: -80px;
                width: 80px;
                transform: rotate(180deg);
                outline: none;
                @extend %atom-home-slider-arrow; // Flches
                @include RWD(tablet){
                    left: 0px;
                    width: 80px;
                }
                .smag-picto{
                    left: 30px;
                }
            }
            .smag-owl-next{
                right: 100px;
                width: 80px;
                outline: none; 
                @extend %atom-home-slider-arrow; // Flches
                @include RWD(tablet){
                    width: 80px;
                }
            } 

            &.disabled{
                display: none;
            }
        }
        .smag-slider{
            // Taille et design des slides
            .owl-item{
                .smag-item{
                    content: '';
                    display: block;
                    display: block;
                    margin-bottom: 20px;
                    opacity: 0.2;
                    transition: opacity 1s;
                    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
                
                    .smag-link{
                        position: relative;
                        display: block;
                        padding-bottom: 100%;
                        background-color: #FFFFFF;
                        transition: all, 0.5s;
                
                        &:hover, &:focus{
                            background-color: #E3E3E3;
                            // opacity: 0.7;
                            text-decoration: none;
                        }
                    }
                    .smag-picture{
                        position: absolute;
                        top: 0; left: 0; height: 100%; width: 100%;
                        background-position: center;
                        background-size: cover;
                        background-repeat: no-repeat;
                        &:after{
                            content: '';
                            position: absolute;
                            top: 0; left: 0;
                            height: 100%; width: 100%;
                            display: block;
                            opacity: 0;
                            transition: all, 0.5s;
                        }
                    }
                    .smag-text{
                        position: absolute;
                        top: 0; left: 0; 
                        z-index: 2; height: 100%; width: 100%;
                        display: flex;
                        flex-direction: column;
                        justify-content: flex-end;
                        padding: 20px 35px;
                        @include RWD(mobile){
                            padding: 20px;
                        }
                    }
                    .smag-content{
                        width: 100%;
                        font-family: $main-font;
                        font-size: 1.4rem;
                        font-weight: 600;
                        line-height: 1.8;
                        color: #818181;
                        letter-spacing: 0.04em;
                
                        b{
                            font-weight: 700;
                        }
                    }
                    .smag-text-bottom{
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        margin-top: 15px;
                    }
                    .smag-category{
                        display: flex;
                        align-items: center;
                        font-family: $fancy-font;
                        font-size: 1.4rem;
                        font-weight: 400;
                        text-transform: uppercase;
                        letter-spacing: 0.04em;
                    }
                    .smag-picto-category{
                        content: '';
                        height: 20px;
                        width: 20px;
                        display: inline-block;
                        margin-right: 5px;
                        background-position: center;
                        background-size: 18px;
                        background-repeat: no-repeat;
                    }
                    .smag-date{
                        display: flex;
                        align-items: center;
                        font-family: $fancy-font;
                        font-size: 1.4rem;
                        font-weight: 400;
                        letter-spacing: 0.04em;
                        
                        .smag-picto-date{
                            content: '';
                            height: 20px;
                            width: 20px;
                            display: inline-block;
                            margin-right: 3px;           
                            background-image: url( svg-bg-uri($svg-clock, #818181) );
                            background-position: center;
                            background-size: 18px;
                            background-repeat: no-repeat;   
                        }
                    }
                    &.smag-has-picture{
                        .smag-picture:before{
                            content: '';
                            position: absolute;
                            top: 0; left: 0; height: 100%; width: 100%;
                            display: block;
                            background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.04) 7%,rgba(0,0,0,0.16) 21%,rgba(0,0,0,0.35) 38%,rgba(0,0,0,0.8) 75%,rgba(0,0,0,0.93) 89%,rgba(0,0,0,1) 100%);
                        }
                        &:not(.smag-facebook){
                            .smag-content{
                                color: #FFFFFF;
                                b{
                                    color: #FFFFFF;
                                }
                            }
                            .smag-date{
                                color: #FFFFFF;
                            } 
                            .smag-category{
                                color: #FFFFFF;
                            }
                            .smag-picto-date{            
                                background-image: url( svg-bg-uri($svg-clock, #FFFFFF) );   
                            }
                        }
                        .smag-link{
                            &:hover, &:focus{
                                .smag-picture:after{
                                    background: rgba(0, 0, 0, 0.4);
                                    opacity: 1; 
                                }
                            }
                        }
                    }
                    &:not(.smag-has-picture){
                        .smag-date{
                            color: #818181;
                        } 
                    }
                    //  Twitter
                    &.smag-twitter{
                        &.smag-has-picture{
                            .smag-picto-category{
                                background-image: url( svg-bg-uri($svg-twit, #FFFFFF) );
                            }   
                        }
                        &:not(.smag-has-picture){ 
                            .smag-picto-category{
                                background-image: url( svg-bg-uri($svg-twit, #0b97bf) );
                            }        
                            .smag-content{
                                b{
                                    color: #0b97bf;
                                }
                            }
                            .smag-category{
                                color: #0b97bf;
                            }
                        }
                    }
                    // Facebook
                    &.smag-facebook{
                        &.smag-has-picture{
                            .smag-picture{
                                top: 20px;
                                left: 20px;
                                right: 20px;
                                bottom: 50%;
                                height: auto;
                                width: auto;
                            }
                            .smag-text{
                                top: 50%;
                                height: 50%;
                                padding-top: 10px;
                            }
                            .smag-picto-category{
                                background-image: url( svg-bg-uri($svg-fb, #385188) );
                            }   
                            .smag-date{
                                color: #818181;
                            }
                        }
                        // &:not(.smag-has-picture){ 
                            .smag-picto-category{
                                background-image: url( svg-bg-uri($svg-fb, #385188) );
                            }        
                            .smag-content{
                                b{
                                    color: #385188;
                                }
                            }
                            .smag-category{
                                color: #385188;
                            }
                            .smag-date{
                                // color: #385188;
                            }
                        // }
                    }
                    // Dailymotion
                    &.smag-dailymotion{
                        .smag-picto-category{
                            background-image: url( svg-bg-uri($svg-daily, #FFFFFF) );
                            background-size: 15px;
                        }     
                        .smag-content{
                            b{
                                color: #FFFFFF;
                            }
                        }
                        .smag-category{
                            color: #FFFFFF;
                        }
                        .smag-picture:before{
                            top: unset;
                            bottom: 0;
                            height: 50%;
                            opacity: 0.5;
                        }
                        .smag-link{
                            position: relative;
                
                            &:before{
                                content: '';
                                position: absolute;
                                top: 50%; left: 50%;
                                z-index: 10;
                                height: 40px;
                                width: 40px;
                                display: inline-block;
                                background-image: url( svg-bg-uri($svg-player, #FFFFFF) );
                                background-position: center;
                                background-size: 40px;
                                background-repeat: no-repeat; 
                                transform: translate(-50%, -50%);
                            }
                        }
                    }
                    // Instagram
                    &.smag-instagram{
                        .smag-picto-category{
                            background-image: url( svg-bg-uri($svg-insta, #FFFFFF) );
                        }     
                        .smag-content{
                            b{
                                color: #FFFFFF;
                            }
                        }
                        .smag-category{
                            color: #FFFFFF;
                        }
                        .smag-picture:before{
                            top: unset;
                            bottom: 0;
                            height: 50%;
                            opacity: 0.5;
                        }
                    }
                }
            }
        }
        .owl-nav{
            .smag-owl-next{
                @include RWD(tablet){
                    right: -20px;
                    width: 100px;
                    .smag-picto{
                        left: 40px;
                    }                    
                }
                @include RWD(mobile){
                    width: 80px;
                }
            }
        }
    }

    // Templates
    #smag-social-templates{
        display: none; 
    } 
}


// Surcharge SMAG
.smag-socials{
    &__title{
        div:first-child{
            font-size: 3rem;
            font-weight: 500;
            color: #7e7e7e;


            @include RWD(mobile){
                font-size: 2rem;
            }
        }
        div{
            line-height: 1;
        }
    }
}