.pagination{    
    width: 100%;
	text-align: center;
	display: flex;
	justify-content: flex-start;
	align-items: center;

		
	.filler{
		flex: 1;
	}
	&-item{
		margin-left: 15px;

		&:first-child{
			margin-left: 0;
		}
	}

	
	// Style des nombres
	&__number{
		text-decoration: none;
		display: block;

		.btn-text{
			font-family: $fancy-font;
			font-size: 1.5rem;
			color: $text-color;
			font-weight: 600;
			padding: 10px;
			display: block;
		} 
		&:hover, &:focus{
			.btn-text{
				color: $core-color-font;
			}
			background-color: $core-color;
		}
	}

	// Style actif
	.is-active{
		pointer-events: none;
		.btn-text{
			text-decoration: underline;
		}
	}
	// Style disabled
	.disabled{
		pointer-events: none;
		opacity: 0;
	}

	@include RWD(tablet){
		.pagin-first,
		.pagin-last{
			display: none !important;
		}
	}
	@include RWD(mobile){
		.pagination__number{
			display: none;
		}
	}
}