.user-alert-banner{    
    background-color: $core-color;
    position: relative;
    padding: 15px 20px;
    padding-right: 40px;
    text-decoration: none;
    display: none;
    @include RWD(tablet){
        padding-right: 20px;
    }

    & > * , a{
        color: $core-color-font;
    }
    .container{
        @include RWD(tablet){
            padding: 0 !important;
        }
    }
    .user-alert-message{
        @extend %text-basis;
        font-weight: 300;
        font-size: 1.1rem; 
        color: $core-color-font;
        width: 100%;
        display: flex;
        text-decoration: none;
        // align-items: center;
        @include RWD(mobile){            
            display: block;
        }
    }
    .user-alert-title{
        color: $core-color-font;
        text-transform: uppercase;
        font-size: 1.6rem;
        line-height: 1.8em;
        padding-left: 20px;
        font-weight: 700;
        @include RWD(mobile){
            padding-left: 0;
            font-size: 1.3rem;
            width: calc(100% - 60px);
        }
    }
    .user-alert-body{
        color: $core-color-font;
        font-size: 1.6rem;
        line-height: 1.2em;
        padding-left: 20px;
        flex: 1;
        @include RWD(tablet_only){
            padding-right: 40px;
        }
        @include RWD(mobile){
            font-size: 1.3rem;
            width: 100%;
            margin-top: 5px;
            padding: 0;
        }
    }
    .user-alert-message {
    }
    .user-alert-body{
        display: flex;
    }
    .close{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 20px;
        transition: all, 0.5s;
        flex-shrink: 0;
        margin-left: 20px;
        content: '';
        display: inline-block;
        width: 27px;
        height: 27px;
        background-size: 25px;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url( svg-bg-uri($svg-close, $core-color-font) );
        &:hover, &:focus{
            opacity: 0.7;
        }
        @include RWD(mobile){
            top: 10px;
            transform: none;
        }
    }
}