.selectize-control{
    .selectize-input{
        @extend .a-input-text;
        height: auto;
        min-height: #{$form-el-height}px;
        padding-right: 40px;
        display: flex;
        align-items: center;
        box-shadow: none;
        flex-wrap: wrap;
        &.input-active{
            border-color: $core-color;
        }
    
        &:after { 
            position: absolute;
            top: 50%;
            right: 15px;
            content: '';
            display: block;
            height: 20px;
            width: 20px;
            background-size: 15px;
            background-position: center;
            background-repeat: no-repeat;
            transform:  translateY(-50%) rotate(90deg);
            background-image: url( svg-bg-uri($svg-arrow1, #7e7e7e) );
        }

    }
    .selection{
        width: 100%;
    }
    .select2-selection{
        background-color: transparent;
        width: 100%;
        padding-right: 40px;
        position: relative;
        z-index: 2;
        display: flex;
        align-items: center;
        border: none !important;
    }
    .select2-selection__rendered{
        padding: 0 !important;
        margin-bottom: 0;
    }
    &.multi .selectize-input.has-items{
        padding-right: 40px;
    }
    &.multi .selectize-input > div,
    &.multi .selectize-input > div.active{
        display: flex;
        align-items: center;
        min-height: 25px;
        font-family: $fancy-font;
        color: $core-color-font;
        background-color: $core-color;
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 1.2;
        margin-bottom: 5px;
    }
    &.plugin-remove_button [data-value] .remove{
        display: flex;
        align-items: center;
        justify-content: center;
        border-left: 1px solid rgba($core-color-font, 0.5);
    }
    &.plugin-remove_button [data-value].active .remove{
        border-left: 1px solid rgba($core-color-font, 0.5);        
    }
    .select2-search{
        width: 100%;
    }
    .select2-search__field{
        margin: 0 !important;
        height: 100% !important;
        padding: 0!important;
        min-height: 0;
        width: 100% !important;
        border: none !important;
        &:hover, &:active, &:focus{
            border-color: transparent !important;
            background-color: transparent !important;
        }
    }
    // Quand une rponse est renseigne on cache le champ de recherche, il ne sert que pour le placeholder
    .select2-selection__choice + .select2-search{
        display: none;
    }
}
.toValidate{
    .valid + .select2-container{
        border-color: $success-color !important;
    }
    .error + .select2-container{
        border-color: $error-color !important;
    }
}