.smag.front {
    .hp-bgdark {
        position: relative;
        padding-bottom: 730px;
        margin-bottom: -643px;
        background-color: $core-color;
        @include RWD(mobile) {
            padding-bottom: 510px;
            margin-bottom: -450px;
        }
        &:before {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            height: 920px;
            width: 100%;
            background-image: url(../images/blurry.jpg);
            background-position: bottom center;
            background-size: cover;
            background-repeat: no-repeat;
            @include RWD(mobile) {
                height: 880px;
            }
        }
    }
    #smag-banner+.smag-hp-breves__container {
        margin-top: 50px;
    }
    .smag-hp-breves__container {
        @include RWD(tablet) {
            margin-bottom: 40px;
        }
    }
    .smag-hp-flexbox-medias {
        @include RWD(desktop) {
            display: flex;
            margin-top: 45px;
            .left {
                width: 50%;
                padding-left: 50px;
                padding-right: 25px;
                .hp-diapo {
                    transform: translateY(-145px);
                }
            }
            .right {
                width: 50%;
                padding-left: 25px;
            }
        }
    }
    .hp-agenda {
        margin-top: 60px;
    }
    .hp-video {
        @include RWD(tablet) {
            margin-top: 40px;
        }
    }
    .smag-hp-flexbox-rencontres {
        @include RWD(desktop) {
            position: relative;
            display: flex;
            align-items: flex-start;
            .hp-rencontres {
                width: 50%;
                // transform: translateY(-60px);
            }
            .hp-kiosque {
                flex: 1;
            }
        }
    }
    #smag-socials {
        margin-top: 95px;
    }
    #seu-footer {
        position: relative;
        padding-top: 190px;
        margin-top: -160px;
        @include RWD(mobile) {
            padding-top: 120px;
            margin-top: -120px;
        }
    }
}