.waved-title{
    font-family: $fancy-font;
    font-weight: 700;
    font-size: 5rem;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    line-height: 1.2em;

    & > * {
        max-width: 100%;
    }

    &:before{
        content: '';
        display: inline-block;
        width: 50px;
        height: 13px;
        flex-shrink: 0;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url( svg-bg-uri($svg-wave, $core-color) );
        display: block;
        margin-bottom: 5px;
    }

    &--small{
        font-size: 1.7rem;
        &:before{margin-bottom: 15px;}
    }
    &--center{
        align-items: center;
    }
    &--after{
        flex-direction: column-reverse;
        &:before{
            margin-top: 5px;
        }
    }
    &--w{
        &-white:before{
            background-image: url( svg-bg-uri($svg-wave, #FFFFFF) );
        }
        &-grey:before{
            background-image: url( svg-bg-uri($svg-wave, #e7e7e7) );
        }
        &-core:before{
            background-image: url( svg-bg-uri($svg-wave, $core-color) );
        }
        &-transparent:before{
            background-image: url( svg-bg-uri($svg-wave, #FFFFFF) );
            opacity: 0.7;
        }
    }
    &--t{
        &-white{
            color: #FFFFFF;
        }
        &-core{
            color: $core-color;
        }
    }
}