.hp-agenda{
    overflow: hidden;
    &__title{
        &:before{
            background-image: url( svg-bg-uri($svg-wave, #475668) );
        }
    }

    @include RWD(mobile){
        transform: none !important;
        top: 0 !important;
    }
    &__title{
        @include RWD(mobile){
            font-size: 3rem;
        }
    }
    &__more{
        text-align: center;
        margin-top: 70px;
    }
    &[data-scroll-animation]{
        position: relative;
        top: 50px;
    }
    .slider-agenda[data-scroll-animation]{
        transform: translateY(50px);
        transition: .3s;
        .owl-item.active{
            transform: translateY(0);
            transition: .3s;
        }
    }
    .slider-agenda[data-animation-state="active"]{
        transform: translateY(-25px);        
        .owl-item.active{
            transform: translateY(55px);
        }
    }
}