.hat{
    line-height: 1.2em;
    text-align: center;
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    max-width: 100vw;
    background-color: $media-bg-color; 
    color: $media-font-color;
    padding: 30px 0px;

    a{
        color: $media-font-color;
    }
    
    & > *:last-child{
        margin-bottom: 0;
    } 
}