// BreakPoints
$break-small: 767;
$break-large: 1280; // Cette valeur doit tre gale  la largeur du container + X pixel. Le passage en tablette se fera dont  X pixel de plus que la largeur du container principal, permettant de ne jamais avoir le contenu coll au bord de l'cran.
$break-vertical: 750;

//~#COLORS
	$core-color: #31455d;
	$core-color-font: #ffffff; 
	$core-color-light: lighten($core-color,65);
	$core-color-dark: #212e3e;
	$second-color: #46cfc0;
	$second-color-font: #191c20;
	$second-color-dark: #2da192;
	$third-color: #fbb52d;
	$third-color-font: #FFFFFF;
	$text-color: #5c5c5c;
	$success-color: $core-color;   
	$error-color: #ff1818;
	$page-bg-color: #FFFFFF;
	$media-bg-color: #E5E5EB;
	$media-font-color: #191C20;

//#COLORS

// Font size des paragraph
$browser-context: 18;
$browser-context-mobile: 16;

// Hauteur des champs de formulaires et marges
$form-el-height: 45;
$gutter-y: 30;
   
// Fonts
$fancy-font: 'MontSerrat', arial;
$main-font: 'OpenSans', arial; 

// Transition
$t-bam: cubic-bezier(.85,.01,0,1);

/*IMAGES RTE*/
$image-xs: 240px;
$image-sm: 480px;
$image-md: 620px;

// Margin RTE
$margin-item-rte: 4.02em;
$margin-p: 1.34em;

$mt-h1: 2em; $mb-h1: 1.5em;
$mt-h2: 2em; $mb-h2: 1em;
$mt-h3: 1.111em; $mb-h3: 0.74em;
$mt-h4: 1.666em; $mb-h4: 0.555em;
$mt-h5: 2em; $mb-h5: 1em;
$mt-h6: 1.8em; $mb-h6: 1em;

// Map engine
$icon-color: $text-color;
$icon-color-active: $second-color;