.a-edito-inlines{
    @extend %text-basis;

    strong:not(.unstyled){
        font-weight: 700;
    }
    em:not(.unstyled){
        font-style: italic;
    }
    sup:not(.unstyled){
        vertical-align: super;
        font-size: smaller;
        line-height: normal; 
    }
    sub:not(.unstyled){
        vertical-align: sub;
        font-size: smaller;
        line-height: normal;
    }
    a:not(.unstyled){
        text-decoration: underline;
        transition: color, 0.3s;
        color: inherit;
        color: $core-color;
            
        &:hover, &:focus{
            color: $text-color;
            text-decoration: underline;
        }

        &[href^="http"]{
            &:not(.a-download__btn):after{
                font-size: smaller;
                text-decoration: none;
                content: '';
                width: 12px;
                height: 12px;
                display: inline-block;
                background-size: 10px;
                background-position: center;
                background-repeat: no-repeat;
                background-image: url( svg-bg-uri($svg-external, $core-color) );
                flex-shrink: 0;
                transition: all, .3s;
                margin-left: 5px;
            }
            &:hover, &:focus{
                &:after{
                    background-image: url( svg-bg-uri($svg-external, $text-color) );
                }
            }
        }
    }
}
.a-edito-lists{
    ul:not(.unstyled), ol:not(.unstyled){
        padding-left: 50px;
        list-style: none;
        margin-bottom: $margin-p;
        @include RWD(mobile){
            padding-left: 20px;
        }
        ul, ol {
            margin-top: $margin-p;
        }
        li{            
            margin-bottom: $margin-p;
            position: relative;
            &:last-child{
                margin-bottom: 0;
            }
            & > :last-child{
                margin-bottom: 0;
            }
        }

    }
    ul:not(.unstyled){   
        li{
            &:before{
                content: '';
                height: 7px;
                width: 7px;
                background-color: $second-color;
                border-radius: 50%;
                margin-right: 6px;
                display: block;
                position: absolute;
                left: -15px;
                top: 7px;
            }
        }
    }
    ol:not(.unstyled){        
        counter-reset: item;
        li{
            &:before{
                content: counter(item) ". ";
                counter-increment: item;
                color: $second-color;
                display: block;
                position: absolute;
                left: -20px;
                top: 0px;
                font-weight: 600;
            }
        }
    }
}
.a-edito-headless{
    @extend .a-edito-inlines;
    @extend .a-edito-lists;    
}

.a-edito{
    @extend .a-edito-headless;
    p:not(.unstyled){
        margin-bottom: $margin-p;
    }
    h1:not(.unstyled){
        @extend .h1;
        margin-top: $mt-h1;
        margin-bottom: $mb-h1;
    }
    h2:not(.unstyled){
        @extend .h2;
        margin-top: $mt-h2;
        margin-bottom: $mb-h2;
    }
    h3:not(.unstyled){
        @extend .h3;
        margin-top: $mt-h3;
        margin-bottom: $mb-h3;
    }
    h4:not(.unstyled){
        @extend .h4;
        margin-top: $mt-h4;
        margin-bottom: $mb-h4;
    }
    h5:not(.unstyled){
        @extend .h5;
        margin-top: $mt-h5;
        margin-bottom: $mb-h5;
    }
    h6:not(.unstyled){
        @extend .h6;
        margin-top: $mt-h6;
        margin-bottom: $mb-h6;
    }
    
    /* Fix pour les Paragraph et liens vide de Yosemite */
    p:not(.unstyled), a:not(.unstyled){
        &:empty:not(.anchor){
            display: none;
        }
    }
}