/* Configuration
============================= */

$sfull_has_overlay: true;
$sfull_animation_time: 1000ms;
$sfull_name: 'slider-agenda';
.#{$sfull_name}{
    position: relative;
    @include RWD(desktop){
        transform: translateY(-25px);
    }
    &-main{
        width: 870px;
        margin: 0 auto;
        max-width: 100%;

        .owl-stage-outer{
            overflow: visible !important; // Gere la disposition en 3 slides visibles
        }
        .owl-item{
            transition: .5s;
            
            &.active{
                transform: translateY(55px);
                box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.45); 
            }
        }
        .owl-item &__slide{
            &:before{
                z-index: 10;
            }
        }
    }
    &-main,
    &-thumbnail, 
    &-slideNpop{
        // position: relative;   Oblig pour que les fleches sortent du cadre

        /* ITEM
        ============================= */

        &__slider-item {
            position: relative;
            @include RWD(mobile){
                height: auto;
            }
        }
        &__slide{
            padding-bottom: 67.81%;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;

            @include RWD(tablet){
                height: auto;
            }
            @include RWD(mobile){
                display: flex;
                align-items: flex-end;
                padding-top: 50px;
                padding-bottom: 0;
                min-height: 67vw;
            }
            @if ($sfull_has_overlay) {
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 2;
                    height: 100%;
                    width: 100%;
                    background-color: rgba(#000000, 0.5);
                }
            }

            &-text{
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 10;
                padding: calc(45vw / 19) calc(90vw / 19);
                font-family: $fancy-font;
                font-weight: 700;
                color: #FFFFFF;
                transition: .5s;
                @include RWD(tablet){
                    padding: calc(60vw / 19) calc(90vw / 19);                    
                }
                @include RWD(mobile){
                    position: relative;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                }
            }
            .flexbox{
                display: flex;
                justify-content: space-between;
                align-items: flex-end;

                @include RWD(mobile){
                    flex-direction: column;
                    align-items: flex-start;

                    & > * {
                        max-width: 100%;
                    }
                }
            }
            .left{
                display: block;
                color: #FFFFFF;
                text-decoration: none;
            }

            /* -- Animation hover titres -- */
            
                .left{
                    position: relative;
                    &:before{
                        content: '';
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        height: 0;
                        width: 4px;
                        display: block;
                        background-color: #FFFFFF;
                        transform: translateX(-15px);
                        transition: .2s;
                    }
                    & > * {
                        transition: .3s;
                    }
                }
                .left:hover, .left:focus{
                    border-width: 4px;
                    &:before{
                        height: 100%;
                    }
                    & > *{
                        @for $i from 1 through 4{
                            &:nth-child(#{$i}){
                                transform: translateY(-#{(4 - $i) * 5}px);
                            }
                        }
                    }
                }

            .right{
                margin-left: 40px;
                flex-shrink: 0;
                max-width: 180px;

                @include RWD(mobile){
                    margin-left: 0;
                }
            }

            &-date-prefix{
                font-size: 1.7rem;
                text-transform: uppercase;

                @include RWD(mobile){
                    font-size: 1.2rem;
                }
            }
            &-date{
                margin-bottom: 25px;
                font-size: 5.0rem;
                @include RWD(mobile){
                    font-size: 4rem;
                }
            }
            &-title{
                margin-bottom: 3px;
                font-size: 3.4rem;
                @include RWD(mobile){
                    font-size: 2.4rem;
                }
            }
            &-category{
                font-size: 1.7rem;
                text-transform: uppercase;
                @include RWD(mobile){
                    margin-bottom: 15px;
                    font-size: 1.5rem;
                }
            }
            &-tag{
                display: flex;
                font-size: 1.2rem;
                line-height: 1.2em;
                color: #FFFFFF;
                text-transform: uppercase;
                text-decoration: none;
                &:hover, &:focus{text-decoration: underline;}

                &:before{
                    content: '';
                    height: 15px;
                    width: 16px;
                    display: inline-block;
                    margin-right: 10px;
                    background-image: url( svg-bg-uri($svg-marker, #FFFFFF) );
                    background-position: center;
                    background-size: contain;
                    background-repeat: no-repeat;
                    flex-shrink: 0;
                }
            }
            &-favorite{
                display: flex;
                margin-top: 30px;
                font-size: 1.2rem;
                line-height: 1.2em;
                color: #FFFFFF;
                text-transform: uppercase;
                text-decoration: none;
                &:hover, &:focus{text-decoration: underline;}
                

                &:before{
                    content: '';
                    height: 15px;
                    width: 16px;
                    display: inline-block;
                    margin-right: 10px;
                    background-image: url( svg-bg-uri($svg-favoris, #FFFFFF) );
                    background-position: center;
                    background-size: contain;
                    background-repeat: no-repeat;
                    flex-shrink: 0;
                }
            }
            &--video {
                @if($sfull_has_overlay) {
                    .owl-video-wrapper {
                        &:before {
                            content: '';
                            position: absolute;
                            top: 0;
                            left: 0;
                            z-index: 2;
                            height: 100%;
                            width: 100%;
                            background-color: rgba(#000000, 0.5);
                        }
                    }
                }
                .owl-video-wrapper{
                    position: absolute;
                    top: 0; left: 0;
                    height: 100%;
                    width: 100%;
                }
                &:before {
                    display: none;
                }
                .owl-video-play-icon {
                    z-index: 3;
                    background-image: url(svg-bg-uri($svg-player, #FFFFFF));
                }
            }
        }

        /* ARROWS
        ============================= */
        &__nav{
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 10;
            width: 1600px;
            transform: translate(-50%, -50%);
            max-width: calc(100% - 80px);

            @include RWD(mobile){
                top: 55px;
                max-width: calc(100% - 40px);
            }
        }
        &__arrow {
            position: absolute;
            top: 50%;
            z-index: 20;
            overflow: visible;
            transform: translateY(-50%);

            @include RWD(mobile){
                top: 0;
            }

            &.disabled{
                display: none !important;
            }

            .flexbox {
                position: relative;
                height: 60px;
                width: 60px;
                overflow: visible;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #FFFFFF;
                border-radius: 50%;
                border: solid 2px #FFFFFF;
                @include RWD(mobile){
                    // height: 30px;
                    // width: 30px;
                }
            }
            &-icon {
                content: '';
                height: 27px;
                width: 27px;
                display: inline-block;
                background-image: url( svg-bg-uri($svg-arrow1, $core-color) );
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                flex-shrink: 0;
                @include RWD(mobile){
                    // height: 12px;
                    // width: 12px;
                }
            }
            &--prev &-icon {
                transform: rotate(-180deg);
            }
            &-pagination {
                position: absolute;
                top: 0;
                height: 100%;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $core-color-font;
                color: $core-color;
                opacity: 0;
                transition: opacity .3s;
                @include RWD(mobile){
                    display: none !important;
                }
                &:after {
                    content: '/';
                    position: absolute;
                    top: 50%; left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            &--prev {
                left: 0;
            }
            &--prev &-pagination {
                right: 0;
                transform: translateX(100%);
            }
            &--next {
                right: 0;
            }
            &--next &-pagination {
                left: 0;
                transform: translateX(-100%);
            }
            &--total {}
            &:hover &-pagination, &:focus &-pagination{
                opacity: 1;
            }
            &.disabled{                
                pointer-events: none;
                .flexbox{
                    background-color: transparent;
                }
            }
            &.disabled &-icon{
                background-image: url( svg-bg-uri($svg-arrow1, #FFFFFF) );
            }
        }

        /* Pager
        ============================= */

        &__pager {
            font-size: 1.7rem;
            font-weight: 700;
            &--current {
                display: none;
            }
            &--total--slideNpop{
                display: none;
            }
            &--prev {
                
            }
            &--next {
            }
            &--next, &--prev{
                position: absolute;
                top: 10px;
                left: 10px;
            }
            &--total{
                position: absolute;
                right: 10px;
                bottom: 10px;
            }
        }

        /* DOTS
        ============================= */

        &__dots {
            position: absolute;
            left: 50%;
            bottom: 40px;
            z-index: 2;
            transform: translateX(-50%);
        }
        &__dot {
            height: 20px;
            width: 20px;
            background-color: #FFFFFF;
            font-size: 0; // Fait disparatre les chiffres dans les cercles
            border-radius: 50%;
            border: solid 2px $core-color;
            &.active {
                background-color: $core-color;
            }
            & + & {
                margin-left: 5px;
            }
        }

        /* Playpause
        ============================= */
        
        &__playpause {
            content: '';
            position: absolute;
            right: 40px;
            bottom: 40px;
            z-index: 2;
            height: 20px;
            width: 20px;
            display: inline-block;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            flex-shrink: 0;
            &.play {
                background-image: url(svg-bg-uri($svg-play, #FFFFFF));
            }
            &.pause {
                background-image: url(svg-bg-uri($svg-pause, #FFFFFF));
            }
        }

        /* Slider Text
        ============================= */

        &__text {
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 2;
            color: #FFFFFF;
            transform: translate(-50%, -50%);
            transition: all 0.5s;
            max-width: calc(100% - 80px);
        }
        &__title {}
        &__description {}
        &__link {
            color: #FFFFFF !important;
            text-decoration: none !important;
        }

        /* Animation, loader et diffrents tats
        ============================= */

        /* -- Loader -- */
        &:not(.loaded){
            overflow: hidden;
            max-height: 400px;
        }
        .loader{
            position: absolute;
            top: 0;
            left: 0;
            z-index: 30;
            height: 100%;
            width: 100%;
            border: solid 2px $core-color;        
        }
        &.loaded .loader{
            display: none !important;
        }

        /* -- Slides -- */

        .owl-item {
            @keyframes customIn {
                0% {
                    transform: translateX(100px);
                    opacity: 0;
                }
                100% {
                    transform: translateX(0);
                    opacity: 1;
                }
            }
            @keyframes customOut {
                0% {
                    opacity: 1;
                }
                100% {
                    opacity: 0;
                }
            }
            &.active {

            }
            &.animated {
                animation-duration: 1s;
            }
            &.customIn {
                animation-name: customIn;
            }
            &.customOut {
                animation-name: customOut;
            }
        }
        .owl-item:not(.active) &__slide-text{
            opacity: 0.2;
            pointer-events: none;
        }

        /* -- Texte -- */

        &.animating &__text {
            transform: translate(-50%, -50%) translateY(-40px);
            opacity: 0;
        }

        /* Elements disabled
        ============================= */
        
        &.no-autoplay &__playpause{display: none !important;}
        &.no-text &__text{display: none !important;}
        &.no-dot &__dots{display: none !important;}
        &.no-pager &__arrow-pagination{display: none !important;}
        &.no-arrow &__arrow{display: none !important;}
    }
    
}