#cookies{
    position: fixed;
    top: 50%;
    right: 100px;
    z-index: 1001;
    width: 400px;
    display: none;
    padding: 25px;
    background-color: $core-color;
    font-family: $fancy-font;
    font-size: 1.6rem;
    color: $core-color-font;
    transform: translateY(-50%);
    border-radius: 5px;
    box-shadow: 0 0 20px 6px rgba(0, 0, 0, 0.1);

    @include RWD(tablet){
        right: 60px;
    }

    @include RWD(mobile){
        right: 20px;
        width: calc(100% - 40px);
        padding: 20px;
    }

    #popup-text{
        margin-bottom: $margin-p;
        text-align: center;

        a{            
            font-family: $fancy-font;
            font-size: 1.2rem;
            color: $core-color-font;
            transition: all, 0.5s;
            text-decoration: underline;

            &:hover, &:focus{
                color: $core-color;
            }       
        }
    }
    .find-more-button{
        display: inline;
        display: none;
        margin-left: 3px;
        font-family: $fancy-font;
        font-size: 1.2rem;
        color: $core-color-font;
        transition: all, 0.5s;
        text-decoration: underline;

        &:hover, &:focus{
            color: $core-color;
        }       
    }

