.a-figure{
    $figure-bg-color: #f4f4f4;
    figure{
        margin-bottom: 20px;
        max-width: 100%;
    
        @include RWD(mobile){
            float: none !important;
            margin-left: auto !important;
            margin-right: auto !important;
        }
        img{
            max-width: 100%;
            vertical-align: middle;
        }
        // Exception au picto external link
        a:after{
            display: none !important;
        }
    }
    figcaption{
        padding: 10px 20px;
        background-color: $figure-bg-color;
        font-size: 13px;
        font-size: 1.3rem;
        font-weight: 300;
        line-height: 1.2em;
        color: #000000;
    }

    // Tailles
    &--xs figure{
        width: $image-xs;
    }
    &--sm figure{
        width: $image-sm;
    }
    &--md figure{
        width: $image-md;
    }
    &--original{
        figure{
            width: auto;
            display: block;
            margin: 0 auto;
            margin-bottom: 20px;
        } 
    }
    &--full figure{
        margin-left: calc(50% - 50vw);
        margin-right: calc(50% - 50vw);
        max-width: 100vw;
    
        figcaption{
            position: relative;
            @extend .container;
            &:before{
                content: '';
                position: absolute;
                top: 0;
                left: -5000px;
                right: -5000px;
                z-index: -1;
                height: 100%;
                display: block;
                background-color: $figure-bg-color;
                @include RWD(mobile){
                    position: static;
                    left: 0;
                    right: 0;
                }
            }
        }
    }

    // Floats
    &--left figure{
        float: left;
        margin-right: 40px;
    }
    &--right figure{
        float: right;
        margin-left: 40px;
    }      

    // Spe drupal
    @at-root{
        .drupal &{
            clear: both;
        }
    }
}