.smag-hp-breves{
    border: solid 15px $core-color-dark;
    padding: 60px 120px;

    @include RWD(tablet){
        padding: 20px 40px;
    }
    &__title{
        margin-bottom: 30px;
    }

    

    @include RWD(desktop){
        // Animation scroll
        &[data-scroll-animation]{
            transform: translateY(100px);
            opacity: 0;
            transition: 1s ease-in-out;
        }
        &[data-animation-state="active"]{
            transform: translateY(0);
            opacity: 1;
        }        
    }
}