.hp-video{
    position: relative;
    padding-bottom: 56.77%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.45); 

    @include RWD(mobile){
        background: none !important;
        padding-bottom: 0;
        margin-top: 80px;
        margin-bottom: 45px;
        display: flex;
        flex-direction: column;
        align-items: center;
        box-shadow: none;
    }

    &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        height: 100%;
        width: 100%;
        background-color: rgba($color: #FFFFFF, $alpha: 0.65);

        @include RWD(mobile){
            display: none;
        }
    }

    &__text{
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 3;
        width: calc(100% - 5.26315789vw);
        display: flex;
        flex-direction: column;
        align-items: center;
        transform: translate(-50%, -50%);

        &> * {
            max-width: 100%;
        }// Ratio vw maquette en 1900px
        @include RWD(mobile){
            position: static;
            transform: none;
            width: 100%;
        }
    }
    &__sup-title{
        margin-bottom: calc(30vw /19);
        color: #212e3e;
        @include RWD(tablet){
            &:before{                
                background-image: url( svg-bg-uri($svg-wave, #475668) );
            }
        }
        @include RWD(mobile){
            align-items: center;
            text-align: center;
            margin-bottom: 20px;
            color: #FFFFFF;
        }
    }
    &__single-link{
        text-align: center;
        text-decoration: none !important;
        @include RWD(tablet_desktop){
            background: none !important;
        }
        @include RWD(mobile){
            padding-bottom: 76%;
            display: block;
            margin-left: -20px;
            margin-right: -20px;
            width: calc(100% + 40px);
            max-width: unset;
            box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.45); 
            position: relative;
            
            &:after{
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 2;
                background-color: rgba($color: #FFFFFF, $alpha: 0.65);
            }            
        }
    }
    &__single-link:hover &__title,
    &__single-link:focus &__title{
        // text-decoration: underline;
        transform: translateY(7px);
    }
    &__single-link:hover &__player,
    &__single-link:focus &__player{
        @include RWD(tablet_desktop){
            transform: translateY(15px);            
        }
    }
    &__title{
        margin-bottom: 5px;
        font-family: $fancy-font;
        font-size: 3.4rem;
        font-weight: 700;
        line-height: 1.2em;
        text-align: center;
        color: #212e3e;
        transition: .3s;

        @include RWD(mobile){
            position: absolute;
            bottom: 8%;
            left: 0;
            font-size: 2.4rem;
            z-index: 3;
        }
    }
    &__player{
        font-size: 5.5rem;
        color: #212e3e;
        transition: .3s;
        display: block;

        @include RWD(mobile){
            position: absolute;
            z-index: 3;
            left: 50%;
            bottom: 8%;
            transform: translateX(-50%) translateY(-85px) translateY(-20px); // 3 lines height du titre
        }
    }
    &__link{
        margin-top: calc(45vw / 19);
        @include RWD(mobile){
            margin-top: 10px;


        }
    }

    
    // Animation scroll
    @include RWD(desktop){
        &[data-scroll-animation]{
            opacity: 0;
            position: relative;
            right: -70px;
            transition: opacity .5s, right .5s;
        }
        &[data-animation-state="active"]{
            opacity: 1;
            right: 0;
        }        
    }
}