.form__type-datelist ,
.form__type-datetime {
    .container-inline {
        display: inline-flex;
        flex-wrap: wrap;
        .form__group {
            margin-bottom: 0;
            @include RWD(mobile){
                width: 100%;
                margin-bottom: em($gutter-y);
            }
            & + .form__group{
                margin-left: 20px;
                @include RWD(mobile){
                    margin-left: 0;
                }
            }
        }
    }
    .silencedSelect{
    }
}