.hp-diapo{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-bottom: 66.67%;
    position: relative;

    @include RWD(mobile){
        background: none !important;
        padding-bottom: 0;
        margin-top: 80px;
        margin-bottom: 45px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.8) 100%);

        @include RWD(mobile){
            display: none;
        }
    }

    &__text{
        z-index: 3;
        position: absolute;
        left: 90px;
        bottom: calc(60vw / 19); // Ratio vw maquette en 1900px
        right: calc(90vw / 19);

        @include RWD(mobile){
            position: static;
            width: 100%;
            display: flex;
            align-items: center;
            flex-direction: column;
        }
    }
    &__sup-title{
        margin-bottom: calc(30vw /19);
        @include RWD(mobile){
            align-items: center;
            text-align: center;
            margin-bottom: 20px;
        }
        @include RWD(tablet){
            &:before{                
                background-image: url( svg-bg-uri($svg-wave, #475668) );
            }
        }
    }
    &__single-link{
        text-decoration: none !important;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        
  

        @include RWD(tablet_desktop){
            background: none !important;
        }
        @include RWD(mobile){
            padding-bottom: 76%;
            display: block;
            margin-left: -20px;
            position: relative;
            margin-right: -20px;
            width: calc(100% + 40px);
            
            &:after{
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 2;
                background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.8) 100%);
            }            
        }
    }
    &__single-link:hover &__title,
    &__single-link:focus &__title{
        transform: translateX(10px);
        &:before{
            height: 100%;
            transform: translateX(-15px) translateX(-10px);
        }
    }
    &__title{
        margin-bottom: calc(30vw /19);
        font-family: $fancy-font;
        font-size: 3.4rem;
        font-weight: 700;
        color: #FFFFFF;
        line-height: 1.2em;
        position: relative;
        transition: .2s;

        @include RWD(mobile){
            position: absolute;
            bottom: 16%;
            left: 0;
            font-size: 2.4rem;
            padding-left: 20px;
            z-index: 3;
        }

        &:before{
            content: '';
            display: block;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 4px;
            height: 0;
            background-color: #FFFFFF;
            transform: translateX(-15px);
            transition: .2s;
        }
    }
    &__link{
        @include RWD(mobile){
            margin-top: 10px;
        }
    }





    @include RWD(desktop){
        // Animation scroll
        &[data-scroll-animation]{
            opacity: 0;
            position: relative;
            left: -70px;
            transition: .5s;
        }
        &[data-animation-state="active"]{
            opacity: 1;
            left: 0;
        }
        
    }
}