/* Configuration
============================= */

$sfull_has_overlay: true;
$sfull_animation_time: 1000ms;
$sfull_name: 'slider-une';
.#{$sfull_name}{
    // Uniquement sur le main
    &-main{

        /* ITEM
        ============================= */

        &__slider-item {
            position: relative;
            min-height: 766px;
            height: 100vh;
            @include RWD(tablet){
                min-height: 600px;
            }
            @include RWD(mobile){
                height: 600px;
            }
        }
        &__slide{
            height: 100%;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;

            @include RWD(tablet){
                // height: auto;
                // padding-bottom: 57%;
            }
            @if ($sfull_has_overlay) {
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 2;
                    height: 100%;
                    width: 100%;
                    background-color: rgba(#000000, 0.5);
                }
            }
            &--video {
                @if($sfull_has_overlay) {
                    .owl-video-wrapper {
                        &:before {
                            content: '';
                            position: absolute;
                            top: 0;
                            left: 0;
                            z-index: 2;
                            height: 100%;
                            width: 100%;
                            background-color: rgba(#000000, 0.5);
                        }
                    }
                }
                .owl-video-wrapper{
                    position: absolute;
                    top: 0; left: 0;
                    height: 100%;
                    width: 100%;
                }
                &:before {
                    display: none;
                }
                .owl-video-play-icon {
                    z-index: 3;
                    background-image: url(svg-bg-uri($svg-player, #FFFFFF));
                }
            }
        }
        &__nav-title{
            display: none;
        }
    }
    &-main,
    &-thumbnail, 
    &-slideNpop{
        position: relative;

        /* ARROWS
        ============================= */

        &__arrow {
            position: absolute;
            top: 50%;
            z-index: 20;
            overflow: visible;


            .flexbox {
                position: relative;
                height: 50px;
                width: 50px;
                overflow: visible;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $core-color;
                @include RWD(mobile){
                    // height: 30px;
                    // width: 30px;
                }
            }
            &-icon {
                content: '';
                height: 20px;
                width: 20px;
                display: inline-block;
                background-image: url(svg-bg-uri($svg-map-chevron, $core-color-font));
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                flex-shrink: 0;
                @include RWD(mobile){
                    // height: 12px;
                    // width: 12px;
                }
            }
            &--prev &-icon {
                transform: rotate(-180deg);
            }
            &-pagination {
                position: absolute;
                top: 0;
                height: 100%;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $core-color-font;
                color: $core-color;
                opacity: 0;
                transition: opacity .3s;
                &:after {
                    content: '/';
                    position: absolute;
                    top: 50%; left: 50%;
                    transform: translate(-50%, -50%);
                }
                @include RWD(mobile){
                    display: none !important;
                }
            }
            &--prev {
                left: 0;
            }
            &--prev &-pagination {
                right: 0;
                transform: translateX(100%);
            }
            &--next {
                right: 0;
            }
            &--next &-pagination {
                left: 0;
                transform: translateX(-100%);
            }
            &--total {}
            &:hover &-pagination, &:focus &-pagination{
                opacity: 1;
            }
        }

        /* Pager
        ============================= */

        &__pager {
            font-size: 1.7rem;
            font-weight: 700;
            &--current {
                display: none;
            }
            &--total--slideNpop{
                display: none;
            }
            &--prev {
                
            }
            &--next {
            }
            &--next, &--prev{
                position: absolute;
                top: 10px;
                left: 10px;
            }
            &--total{
                position: absolute;
                right: 10px;
                bottom: 10px;
            }
        }

        /* DOTS
        ============================= */

        &__dots {
            position: absolute;
            left: 50%;
            bottom: 40px;
            z-index: 2;
            transform: translateX(-50%);
        }
        &__dot {
            height: 20px;
            width: 20px;
            background-color: #FFFFFF;
            font-size: 0; // Fait disparatre les chiffres dans les cercles
            border-radius: 50%;
            border: solid 2px $core-color;
            &.active {
                background-color: $core-color;
            }
            & + & {
                margin-left: 5px;
            }
        }

        /* Playpause
        ============================= */
        
        &__playpause {
            content: '';
            position: absolute;
            right: 40px;
            bottom: 40px;
            z-index: 2;
            height: 20px;
            width: 20px;
            display: inline-block;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            flex-shrink: 0;
            &.play {
                background-image: url(svg-bg-uri($svg-play, #FFFFFF));
            }
            &.pause {
                background-image: url(svg-bg-uri($svg-pause, #FFFFFF));
            }
        }

        /* Slider Text
        ============================= */

        &__text {
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 2;
            color: #FFFFFF;
            transform: translate(-50%, -50%);
            transition: all 0.25s;
            max-width: calc(100% - 160px);
            width: 100%;

            @include RWD(tablet){
                max-width: calc(100% - 80px);
            }

            @include RWD(mobile){
                max-width: calc(100% - 40px);
            }
        }
        &__suptitle{
            align-items: center;
            margin-bottom: 25px;
            &:before{
                margin-top: 20px !important;
            }
        }
        &__title {
            font-family: $fancy-font;
            font-size: 8rem;
            text-align: center;
            font-weight: 700;
            line-height: 1.2em;

            @include RWD(tablet){
                font-size: 4rem;
            }
        }
        &__description {
            font-family: $fancy-font;
            font-size: 2.5rem;
            line-height: 1.2em;
            text-align: center;
            margin-top: 5px;

            @include RWD(mobile){
                font-size: 2rem; 
                display: none;               
            }

        }
        &__link {
            color: #FFFFFF !important;
            text-decoration: none !important;
            & > * {
                @for $i from 1 through 4{
                    &:nth-child(#{$i}){
                        transition: .3s;
                    }
                }
            }

            &:hover, &:focus{
                & > * {
                    @for $i from 1 through 4{
                        &:nth-child(#{$i}){
                            transform: translateY(-#{(4 - $i) * 8}px);
                        }
                    }
                }
            }
        }

        /* Animation, loader et diffrents tats
        ============================= */

        /* -- Loader -- */
        &:not(.loaded){
            overflow: hidden;
            height: 100vh;
            @keyframes slideIn{
                0%{
                    transform: translateY(40px);
                    opacity: 0;
                }
                100%{
                    transform: translateY(0);
                    opacity: 1;
                }
            }
            .slider-une-thumbnail__slider{
                transform: translateY(40px);
                opacity: 0;
                @for $i from 1 through 20{
                    .owl-item:nth-child(#{$i}){
                        animation-delay: #{($i + 1) * 0.1s}
                    }
                }
            }
        }
        &.loaded{
            .slider-une-thumbnail__slider{
                animation: slideIn .5s;
                animation-fill-mode: forwards;
            }
        }
        .loader{
            position: absolute;
            top: 0;
            left: 0;
            z-index: 30;
            height: 100%;
            width: 100%;
            background-color: $core-color;   
            &:before{
                content: '';
                display: inline-block;
                width: 5vw;
                height: 5vw;
                flex-shrink: 0;
                background-size: contain;
                background-position: center;
                background-repeat: no-repeat;
                background-image: url( svg-bg-uri($svg-wave, #FFFFFF) );
                position: absolute;
                top: 50%;
                left: 50%;
                opacity: 0.5;
            }
            &:after{
                content: '';
                display: inline-block;
                width: 5vw;
                height: 5vw;
                flex-shrink: 0;
                background-size: 5vw auto;
                background-position: left center;
                background-repeat: no-repeat;
                background-image: url( svg-bg-uri($svg-wave, #FFFFFF) );
                position: absolute;
                top: 50%;
                left: 50%;
                animation: waving 1s infinite;
                overflow: hidden;
            }
            @keyframes waving{
                0%{
                    width: 0vw;
                }
                100%{
                    width: 5vw;
                }
            }
        }
        &.loaded .loader{
            display: none !important;
        }

        /* -- Slides -- */

        .owl-item {
            @keyframes unecustomIn {
                0% {
                    // transform: translateX(100px);
                    opacity: 0;
                }
                100% {
                    // transform: translateX(0);
                    opacity: 1;
                }
            }
            @keyframes unecustomOut {
                0% {
                    opacity: 1;
                }
                100% {
                    opacity: 0;
                }
            }
            &.active {}
            &.animated {
                animation-duration: 0.5s;
            }
            &.customIn {
                animation-name: unecustomIn;
            }
            &.customOut {
                animation-name: unecustomOut;
            }
        }

        /* -- Texte -- */

        &.animating &__text {
            transform: translate(-50%, -50%) translateY(-40px);
            opacity: 0;
        }

        /* Elements disabled
        ============================= */
        
        &.no-autoplay &__playpause{display: none !important;}
        &.no-text &__text{display: none !important;}
        &.no-dot &__dots{display: none !important;}
        &.no-pager &__arrow-pagination{display: none !important;}
        &.no-arrow &__arrow{display: none !important;}

    }
    &__more{
        position: relative;
        display: flex;
        justify-content: center;
        &:before{
            content: '';
            height: 1px;
            width: 100%;
            position: absolute;
            left: 0;
            top: 50%;
            background-color: #505d6d;
        }
    }
    
}