.bottomless-menu{

    /* Padding lattral */      $btl_padding: 40; 
    /* Hauteur du menu */       $btl_height: 100%;
    /* Width du menu */         $btl_width: 100%;

    /* Couleurs */              $tab-color: #8494a3;                        //Couleur de fond du tableau
                                $tab-dark-color: #7b8c9c;                   // Fond des flches
                                $tab-darker-color: darken(#7b8c9c, 20%);    //hover des flches

    /* Fonts */                 $tab-font: $fancy-font;
    
    /* Svg arrows */            $btl-arrow-svg: $svg-chevron3;
    position: absolute;
    top: 0px;
    left: 0;
    height: $btl_height;
    width: $btl_width;
    overflow: hidden;
    padding: 0 #{$btl_padding}px;
    
    /* ------------------------------------- */

    background-color: $tab-color;

    &.focus{
        overflow: auto;
        overflow-x: hidden;
    }
    .menu{ 
        li{
            width: calc(100% + #{2*$btl_padding}px);
            display: flex; 
            align-items: stretch;
            margin: 0 -#{$btl_padding}px;
            border-top: solid 1px $tab-dark-color;  
            &:first-child{
                border-top: none; 
                .menu-item{
                    font-weight: 400;
                }
            }
        }
        .menu-item{
            position: relative;
            flex: 1;
            display: block;
            padding: 35px 0px 35px 0; 
            padding-left: #{$btl_padding}px;
            font-family: $tab-font;
            font-size: 1.6rem;
            font-weight: 600;
            color: #FFFFFF;
            transition: all, 0.5s;
            text-decoration: none;
            &[href]{
                &:hover, &:focus{
                    background-color: #FFFFFF;
                    color: $tab-color;
                }
            }
        }
        .menu-arrow{
            position: relative;
            transition: all, 0.5s;
            flex-shrink: 0;
            .flexbox{
                display: flex;
                justify-content: center;
                align-items: center;
                padding-left: 20px;
                padding-right: 20px;
                flex-shrink: 0;
                & > div{
                    content: '';
                    height: 30px;
                    width: 30px; 
                    display: inline-block;
                    background-color: $tab-dark-color;
                    background-image: url( svg-bg-uri($btl-arrow-svg, #FFFFFF) );
                    background-position: center;
                    background-size: 5px;
                    background-repeat: no-repeat;
                    transition: all, 0.5s;
                    border-radius: 3px;
                }
            }
            &:before{
                content: '';
                position: absolute;
                top: 50%;
                left: 0px;
                height: 80%;
                width: 1px;
                display: block;
                background-color: $tab-dark-color;
                transform: translateY(-50%);
                transition: all, 0.5s;
            }
            &:hover, &:focus{
                // background-color: #FFFFFF;
                & > div{
                    background-color: $tab-darker-color;
                }
            }
        }
        .menu-back{
            width: 100%;
            .flexbox{
                padding: 0 #{$btl_padding}px;
                background-color: $tab-dark-color;

                & > div{
                    background-color: $tab-color;
                    transform: rotate(-180deg);
                    &:before{
                        display: none;
                    }
                }

            }
            &:before{
                display: none;
            }
            .menu-item{
                margin-left: 10px;
            }
            &:hover, &:focus{
                .flexbox{
                    background-color: #FFFFFF;
                }
                .menu-item{
                    color: $tab-color;
                }
            }
        }
    }
    .sub-overflow-container{
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        height: $btl_height;
        width: $btl_width;
        overflow: hidden; 
        padding: 0 #{$btl_padding}px;
        background-color: $tab-color;
        transform: translateX(-100%);
        transition: all, 0.5s;
        
        &.focus{
            overflow: auto;
            overflow-x: hidden;
        }

        &.opened{
            transform: translateX(0);
        }
    }
}